import React, { useContext, useState } from "react";

const DeleteCategoryContext = React.createContext();

export const useDeleteCategory = () => {
  return useContext(DeleteCategoryContext);
};

export const DeleteCategoryProvider = ({ children }) => {
  const [state, setState] = useState({ stackOfIds: [] });

  const del = (stackOfIds) => setState({ stackOfIds });
  const reset = () => {
    setState({ stackOfIds: [] });
  };
  return (
    <DeleteCategoryContext.Provider
      value={{ stackOfIds: state.stackOfIds, delete: del, reset: reset }}
    >
      {children}
    </DeleteCategoryContext.Provider>
  );
};
