import { makeStyles, useTheme } from "@material-ui/core";
import AppBar from "@material-ui/core/AppBar";
import { useEffect, useState } from "react";
import { useComponentSizes } from "../Context/ComponentSizesContext";
import { useShowSignInBar } from "../Context/ShowSignInBarContext";

export function MoveOnScrollTopAppBar(props) {
  const theme = useTheme();
  const showSignInBar = useShowSignInBar();
  const componentSizes = useComponentSizes();
  const useStyles = makeStyles({
    colorPrimary: {
      backgroundColor:
        theme.palette.type === "dark"
          ? theme.palette.background.paper
          : props.whiteForLight
          ? "white"
          : theme.palette.primary.main,
    },
  });
  
  const classes = useStyles();
  const [Styles, setStyles] = useState({
    position: "sticky",
    ...(showSignInBar.status === false
      ? { transform: 0, top: 0 }
      : { transform: 0, top: componentSizes.topSignInAppBarHeight }),
  });
  

  const [Styles2, setStyles2] = useState({
    height:
      50 +
      (showSignInBar.status === false
        ? 0
        : componentSizes.topSignInAppBarHeight),
  });

  

  useEffect(() => {
    setStyles((prev) => ({
      ...prev,
      ...(showSignInBar.status === false
        ? { transform: 0, top: 0 }
        : { transform: 0, top: componentSizes.topSignInAppBarHeight }),
    }));
  }, [
    componentSizes.topSignInAppBarHeight,
    showSignInBar.status,
    theme.palette.type,
  ]);

  useEffect(() => {
    setStyles2((prev) => ({
      ...prev,
      height:
        showSignInBar.status === false
          ? 0
          : componentSizes.topSignInAppBarHeight,
    }));
  }, [componentSizes.topSignInAppBarHeight, showSignInBar.status]);

  return (
    <>
      <div>
        <AppBar style={Styles} className={classes.colorPrimary}>
          {props.children}
        </AppBar>
        <div style={Styles2}></div>
      </div>
    </>
  );
}
