import React, { useContext, useState } from "react";

const AddCategoryContext = React.createContext();

export const useAddCategory = () => {
  return useContext(AddCategoryContext);
};

export const AddCategoryProvider = ({ children }) => {
  const [state, setState] = useState({
    title: "",
    stackOfIds: [],
    horizontal: true,
  });

  const add = (title, stackOfIds, horizontal) =>
    setState({ title, stackOfIds, horizontal });

    const reset = () => {
      setState({
        title: "",
        stackOfIds: [],
        horizontal: true,
      });
    };
  return (
    <AddCategoryContext.Provider
      value={{
        title: state.title,
        stackOfIds: state.stackOfIds,
        horizontal: state.horizontal,
        add: add,
        reset: reset
      }}
    >
      {children}
    </AddCategoryContext.Provider>
  );
};
