import Typography from "@material-ui/core/Typography";
import { useEffect, useLayoutEffect, useRef, useState } from "react";
import { useHistory, useParams } from "react-router-dom";
import { useStoreBookmarksToServer } from "../DataSource/Context/StoreBookmarksToServer";
import { useGetDocFromLocalForage } from "../DataSource/getLocalCollection";
import { useGetRemoteBookmarksStat } from "../DataSource/getRemoteBookmarksStat";
//const worker = new Worker("/thread.worker.js");
import { useLocalForage } from "../DataSource/localForageContext";
import { useFetch } from "../DataSource/useFetch";
import { useFireBaseAuth } from "../General/Autorization/Context/FireBase";
import { CardsList } from "../General/Cards/CardsList";
import { useSignInModal } from "../General/Context/SignInModal";
import { useTopTranformerTrigger } from "../General/Context/TopTranformerTriggerContext";
import { useWheelDirection } from "../General/Context/WheelDirectionContext";
import { useWindowWidth } from "../General/Context/WindowWidthContext";
import { findStacksOfIdsForCardsCollections } from "../General/Service/findStacksOfIdsForCardsCollections";
import { TopTabsMenuSkeleton } from "../General/TopTabsMenuSkeleton";
import { CollectionLoader } from "../Loader/collection";
import { addLocalBokmarks } from "./addLocalBokmarks";
import { bookmarksInterface } from "./bookmarksInterface";
import { TopAppBar } from "./Navigation/TopAppBar";
import { getLocalIds } from "./Service/getLocalIds";
import { navigationToUrlStackOfTitles } from "./Service/navigationToUrlStackOfTitles";
 
function nFormatter(num: any, digits: any) {
  var si = [
    { value: 1, symbol: "" },
    { value: 1e3, symbol: "k" },
    { value: 1e6, symbol: "M" },
    { value: 1e9, symbol: "G" },
    { value: 1e12, symbol: "T" },
    { value: 1e15, symbol: "P" },
    { value: 1e18, symbol: "E" },
  ];
  var rx = /\.0+$|(\.[0-9]*[1-9])0+$/;
  var i;
  for (i = si.length - 1; i > 0; i--) {
    if (num >= si[i].value) {
      break;
    }
  }
  return (num / si[i].value).toFixed(digits).replace(rx, "$1") + si[i].symbol;
}

interface localBookmarksInterface {
  [key: string]: {
    b?: string;
  };
}

export default function CollectionList() {
  let { user, kit } = useParams<Record<string, string | undefined>>();

  const wheelDirection = useWheelDirection();
  const topTranformerTrigger = useTopTranformerTrigger();
  const store = useLocalForage();
  const [awesome, setAwesome] = useState<bookmarksInterface>({});
  const awesomeRef = useRef<bookmarksInterface>({});
  const history = useHistory();
  const [localData, localLoading] = useGetDocFromLocalForage("doc");
  const [data, loading] = useFetch("/" + user + "-" + kit + ".json");
  const [setedUpBookmarks, setUpBookmarks] = useState(false);
  const storeBookmarksToServer = useStoreBookmarksToServer();
  const modal = useSignInModal();
  const fireBaseAuth = useFireBaseAuth();
  const [topAppBarData, setTopAppBarData] = useState({});
  const [stacksOfLocalIds, setStacksOfLocalIds] = useState({});

  const windowWidth = useWindowWidth();
  const theme = useTheme();
  const existsLocalSavedInArray_ = useRef([]);
  const [urlStackOfTitles, setUrlStackOfTitles] = useState({});
  const [remoteBookmarksStat, remoteBookmarksStatLoading] =
    useGetRemoteBookmarksStat();

  const useStyles = makeStyles((theme) => ({
    action: {
      marginRight: 0,
      marginLeft: 16,
      marginTop: 4,
      color: theme.palette.text.secondary,
    },
    root: {
      padding: 16,
    },
    root2: {
      paddingTop: 0,
      paddingBottom: 0,
    },
    avatar: {
      alignSelf: "start",
    },
    media: {
      height: 155,
      backgroundColor:
        theme.palette.type === "dark"
          ? theme.palette.background.default
          : theme.palette.primary.main,
      paddingTop: "56.25%", // 16:9
    },
    card: {
      position: "relative",
    },
    overlay: {
      position: "absolute",
      bottom: "20px",
      left: "20px",
      right: "20px",
      color: "white",
      fontSize: 22,
    },
  }));

  const classes = useStyles();

  useLayoutEffect(() => {
    topTranformerTrigger.reset();
    wheelDirection.setDirection("down");
  }, []);

  useEffect(() => {
    if (localLoading) {
      return;
    }

    let stackOfLocalIds_: any = {};
    if (localData && Object.keys(localData).length > 0) {
      findStacksOfIdsForCardsCollections(localData, [], stackOfLocalIds_);
      if (process.env.REACT_APP_DEBUG)
        console.log("stacksOfLocalIds:::", stackOfLocalIds_);
      setStacksOfLocalIds(stackOfLocalIds_);
    }

    let collection: bookmarksInterface = {};
    if (Object.keys(data).length > 0) {
      let urlStackOfTitles = {};
      let stackOfTitles: any;
      navigationToUrlStackOfTitles(
        data.navigation,
        stackOfTitles,
        urlStackOfTitles
      );
      if (process.env.REACT_APP_DEBUG)
        console.log("nav urlStackOfTitles", urlStackOfTitles);
      setUrlStackOfTitles(urlStackOfTitles);

      collection = data.collections;
      addLocalBokmarks(localData, collection);
      setAwesome(collection);
      awesomeRef.current = collection;

      setTopAppBarData({
        title: data.title,
        url: data.url,
        type: data.type,
      });
    }
  }, [data, store.bookmarks, localLoading]);

  useEffect(() => {
    if (
      !remoteBookmarksStat ||
      awesome === null ||
      Object.keys(awesome).length === 0
    ) {
      return;
    }
    let clone = { ...awesome };

    Object.entries(clone).map(([k, v]) => {
      let url = v.url;
      url = url?.replace(/[~\*\/\[\]]/g, "#");

      v.bookmarks = remoteBookmarksStat[url!] ?? 0;
    });

    setAwesome(clone);
  }, [remoteBookmarksStat, localData, setedUpBookmarks, awesomeRef.current]);

  let listOfSeenIds_: any[] = [];
  useEffect(() => {
    if (localData) {
      getLocalIds(localData, existsLocalSavedInArray_.current, listOfSeenIds_);
      if (process.env.REACT_APP_DEBUG) console.log("localData::::", localData);
    }
  }, [localData, loading]);

  if (loading) {
    return (
      <>
        <TopAppBar />
        <TopTabsMenuSkeleton />
        <CollectionLoader />
      </>
    );
  }

  if (process.env.REACT_APP_DEBUG)
    console.log("urlStackOfTitles", urlStackOfTitles);
  return (
    <>
      <TopAppBar topAppBarData={topAppBarData} />
      <CardsList
        cards={awesome}
        existsLocalSavedInArray_={existsLocalSavedInArray_.current}
        urlStackOfTitles={urlStackOfTitles}
        stacksOfLocalIds={stacksOfLocalIds}
      />
      <Container
        maxWidth="sm"
        style={{
          paddingLeft: 8,
          paddingRight: 8,
          display: "flex",
          flexDirection: "row",
          marginTop: 10,
          alignItems: "stretch",
        }}
      >
        <Card
          className="card"
          id={"card-"}
          variant="outlined"
          style={{ maxWidth: 590 }}
        >
          <Box
            lineHeight="normal"
            style={{
              padding: 16,
              height: 100,
              display: "flex",
              alignItems: "center",
              justifyContent: "center",
            }}
          >
            <Typography variant="h6">
              New collections coming soon.{" "}
              {fireBaseAuth.isSignIn === false && (
                <>
                  {" "}
                  <Link
                    style={{ cursor: "pointer" }}
                    onClick={() => {
                      modal.show();
                    }}
                    onTouchStart={() => {
                      modal.show();
                    }}
                  >
                    Sign in
                  </Link>{" "}
                  so you don't miss them!
                </>
              )}
            </Typography>
          </Box>
        </Card>
      </Container>
    </>
  );
}
