import React, { useContext, useState } from "react";

const MoveCardContext = React.createContext();

export const useMoveCard = () => {
  return useContext(MoveCardContext);
};
export const MoveCardProvider = ({ children }) => {
  const [state, setState] = useState({
    card: {},
    fromStackOfIds: [],
    toStackOfIds: [],
    toCategoryTitle: "",
  });

  const move = (card, fromStackOfIds, toStackOfIds, toCategoryTitle) =>
    setState({
      card,
      fromStackOfIds,
      toStackOfIds,
      toCategoryTitle
    });
  const reset = () => {
    setState({
      card: {},
      fromStackOfIds: [],
      toStackOfIds: [],
      toCategoryTitle: "",
    });
  };
  return (
    <MoveCardContext.Provider
      value={{
        card: state.card,
        fromStackOfIds: state.fromStackOfIds,
        toStackOfIds: state.toStackOfIds,
        toCategoryTitle: state.toCategoryTitle,
        move: move,
        reset: reset,
      }}
    >
      {children}
    </MoveCardContext.Provider>
  );
};
