import React, { useContext, useState } from "react";

const StoreVotingStatsToServerContext = React.createContext();

export const useStoreVotingStatsToServer = () => {
  return useContext(StoreVotingStatsToServerContext);
};

export const StoreVotingStatsToServerProvider = ({ children }) => {
  const [update, setUpdate] = useState();

  const store = () => {
    setUpdate({ true: true });
  };

  return (
    <StoreVotingStatsToServerContext.Provider value={{ store }}>
      {children}
    </StoreVotingStatsToServerContext.Provider>
  );
};
