import React, { useState, useEffect } from "react";
import Toolbar from "@material-ui/core/Toolbar";
import { makeStyles } from "@material-ui/core/styles";
import { useTheme } from "@material-ui/core";

import AppBar from "@material-ui/core/AppBar";
import Typography from "@material-ui/core/Typography";
import Button from "@material-ui/core/Button";
import { useShowSignInBar } from "../Context/ShowSignInBarContext";
import firebase from "firebase/app";
import "firebase/auth";
import "./TopSignInAppBar.css";
import { StylesProvider } from "@material-ui/core/styles";
import { useFireBaseAuth } from "../Autorization/Context/FireBase";
import Link from "@material-ui/core/Link";

import logoFaceBook from "super-tiny-icons/images/svg/facebook.svg";
import logoGmail from "super-tiny-icons/images/svg/google.svg";
import logoGithub from "super-tiny-icons/images/svg/github.svg";

import List from "@material-ui/core/List";
import Divider from "@material-ui/core/Divider";
import ListItem from "@material-ui/core/ListItem";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import ListItemText from "@material-ui/core/ListItemText";
import { useWindowWidth } from "../Context/WindowWidthContext";
import { useComponentSizes } from "../Context/ComponentSizesContext";
import { useSignInModal } from "../Context/SignInModal";
import Terms from "../Terms";
import { firebaseAndGapiAuth } from "../Autorization/Service/firebaseAndGapi";

export default function TopSignInAppBar() {
  const showSignInBar = useShowSignInBar();
  const fireBaseAuth = useFireBaseAuth();
  const theme = useTheme();
  const windowWidth = useWindowWidth();
  const [text, setText] = useState();
  const [signInPadding, setSignInPadding] = useState(8);
  const [isShowBigButtons, setIsShowBigButtons] = useState();
  const componentSizes = useComponentSizes();
  const modal = useSignInModal();

  const useStyles = makeStyles({
    root: {
      color: theme.palette.type === "light" ? "black" : "white",
    },
  });
  const classes = useStyles();

  const [styles, setStyles] = useState({
    borderBottom: theme.palette.divider + " 1px solid",
    position: "fixed",
    //transform: "translateY(" + componentSizes.topSignInAppBarHeight + "px)",
    transition: "transform 200ms",
    zIndex: 1101,
    top: 0,
    boxShadow: "none",
    height: componentSizes.topSignInAppBarHeight,
    backgroundColor: theme.palette.type === "light" ? "white" : "black",
  });

  useEffect(() => {
    setStyles((prev) => ({
      ...prev,
      height: componentSizes.topSignInAppBarHeight,
    }));
  }, [componentSizes.topSignInAppBarHeight]);
  useEffect(() => {
    if (
      showSignInBar.status === true &&
      fireBaseAuth.loading === false &&
      fireBaseAuth.isSignIn === false
    ) {
      setStyles((prev) => ({ ...prev, transform: "translateY(0px)" }));
    } else {
      setStyles((prev) => ({
        ...prev,
        transform:
          "translateY(-" + componentSizes.topSignInAppBarHeight + "px)",
      }));
    }
  }, [
    fireBaseAuth,
    showSignInBar.status,
    componentSizes.topSignInAppBarHeight,
  ]);
  useEffect(() => {
    setStyles((prev) => ({
      ...prev,
      borderTop: theme.palette.divider + " 1px solid",
      borderBottom: theme.palette.divider + " 1px solid",
      backgroundColor: theme.palette.type === "light" ? "white" : "black",
    }));
  }, [theme]);
  //const classes = useTabStyles();

  useEffect(() => {
    if (windowWidth.width >= 1260) {
      setText({
        github: "Sign In with Github",
        facebook: "Sign In with Facebook",
        google: "Sign In with Google",
      });
      setIsShowBigButtons(true);
    } else if (windowWidth.width >= 970) {
      setText({
        github: "Sign In",
        facebook: "Sign In",
        google: "Sign In",
      });
      setIsShowBigButtons(true);
    } else if (windowWidth.width >= 600) {
      setText({
        github: "",
        facebook: "",
        google: "",
      });
      setIsShowBigButtons(false);
      setSignInPadding("8px 16px");
    } else if (windowWidth.width >= 500) {
      setText({
        github: "",
        facebook: "",
        google: "",
        general: "Sign&nbsp;in",
      });
      setIsShowBigButtons(false);
      setSignInPadding(4);
    } else {
      setText({
        github: "",
        facebook: "",
        google: "",
        general: "Sign&nbsp;in",
      });
      setIsShowBigButtons(false);
      setSignInPadding(0);
    }
  }, [windowWidth]);

  return (
    <div id="TopSignInAppBar">
      <AppBar position="fixed" style={styles} color="inherit">
        <Toolbar>
          <Typography variant="h5">
            Register to save your collection in the cloud. Free, no credit card
            required.
            <br />
            {isShowBigButtons === true && (
              <span style={{ fontSize: 12 }}>
                <Terms />
              </span>
            )}
          </Typography>

          {isShowBigButtons !== undefined && (
            <>
              {isShowBigButtons === true ? (
                <>
                  <List style={{ padding: 8 }}>
                    <ListItem
                      style={{ height: 48 }}
                      button
                      key="GoogleAuthProvider"
                      onClick={() => {
                        setTimeout(() => {
                          try {
                            firebaseAndGapiAuth();
                          } catch (err) {
                            console.log("Error11... Please try again.", err);
                          }
                        }, 50);
                      }}
                    >
                      <ListItemIcon style={{ minWidth: 35, height: 26 }}>
                        <img src={logoGmail} style={{ width: 20, margin: 3 }} />
                      </ListItemIcon>
                      <ListItemText primary={text.google} />
                    </ListItem>
                  </List>

                  <List>
                    <ListItem
                      style={{ height: 48 }}
                      button
                      key="GithubAuthProvider"
                      onClick={() => {
                        setTimeout(() => {
                          try {
                            const GithubAuthProvider =
                              new firebase.auth.GithubAuthProvider();
                            firebase.auth().signInWithPopup(GithubAuthProvider);
                          } catch (err) {
                            alert("Error12... Please try again.");
                          }
                        }, 50);
                      }}
                    >
                      <ListItemIcon style={{ minWidth: 35, height: 26 }}>
                        <img
                          src={logoGithub}
                          style={{ width: 20, margin: 3 }}
                        />
                      </ListItemIcon>
                      <ListItemText primary={text.github} />
                    </ListItem>
                  </List>

                  <List style={{ padding: 8 }}>
                    <ListItem
                      style={{ height: 48 }}
                      button
                      key="FacebookAuthProvider"
                      onClick={() => {
                        setTimeout(() => {
                          const FacebookAuthProvider =
                            new firebase.auth.FacebookAuthProvider();
                          try {
                            firebase
                              .auth()
                              .signInWithPopup(FacebookAuthProvider);
                          } catch (err) {
                            alert("Error13... Please try again.");
                          }
                        }, 50);
                      }}
                    >
                      <ListItemIcon style={{ minWidth: 35, height: 26 }}>
                        <img
                          src={logoFaceBook}
                          style={{ width: 20, margin: 3 }}
                        />
                      </ListItemIcon>
                      <ListItemText primary={text.facebook} />
                    </ListItem>
                  </List>
                </>
              ) : (
                <List>
                  <ListItem
                    style={{
                      flexDirection: "row",
                      padding: signInPadding,
                      marginLeft: 6,
                      height: 48,
                    }}
                    button
                    key="open-auth-providers"
                    onClick={() => {
                      modal.show();
                    }}
                    onTouchStart={() => {
                      modal.show();
                    }}
                  >
                    <ListItemText
                      primary="Sign&nbsp;in&nbsp;with"
                      style={{ marginRight: 5 }}
                    />
                    <div id="open-auth-providers">
                      <ListItemIcon style={{ minWidth: 35, height: 24 }}>
                        <img src={logoGmail} style={{ width: 15, margin: 3 }} />
                        <img
                          src={logoGithub}
                          style={{ width: 15, margin: 3 }}
                        />
                        <img
                          src={logoFaceBook}
                          style={{ width: 15, margin: 3 }}
                        />
                      </ListItemIcon>
                    </div>
                  </ListItem>
                </List>
              )}
            </>
          )}
        </Toolbar>
      </AppBar>
    </div>
  );
}
