import { makeStyles, Tab } from "@material-ui/core";
import { MoveOnScrollTopTabsMenu } from "./MoveOnScrollTopTabsMenu";
import Badge from "@material-ui/core/Badge";
import { useEffect, useMemo, useState } from "react";
import { useLocalForage } from "../../DataSource/localForageContext";
import { useCardsNumberInUserCollection } from "../Context/CardsNumberInUserCollectionContext";
const useStyles = makeStyles((theme) => ({
  root: {
    padding: "6px 13px",
  },
}));
export const TopTabsMenu = () => {
  const store = useLocalForage();
  const [cardsNumberCurrent, setCardsNumberCurrent] = useState(0);
  const cardsNumberInUserCollection = useCardsNumberInUserCollection();

  const classes = useStyles();
  const anchorOrigin = { vertical: "top", horizontal: "left" };

  useEffect(() => {
    store.settings.getItem("cardsNumberCurrent").then(function (cardsNumber) {
      setCardsNumberCurrent(cardsNumber);
    });
  }, [cardsNumberInUserCollection]);

  return useMemo(() => {
    return (
      <>
        {console.log("Tab")}
        <MoveOnScrollTopTabsMenu>
          <Tab
            id="my-collection-tab"
            className={classes.root}
            label={
              <Badge
                badgeContent={cardsNumberCurrent}
                color="secondary"
                anchorOrigin={anchorOrigin}
              >
                Saved
              </Badge>
            }
            value="/"
          />

          <Tab id="awesome-tab" label="Browse" value="/browse" />
          <Tab id="bookmarks-tab" label="Bookmarks" value="/bookmarks" />
        </MoveOnScrollTopTabsMenu>
      </>
    );
  }, [cardsNumberCurrent]);
};
