import { useState, useEffect } from "react";
import { bookmarksInterface } from "../Awesome/bookmarksInterface";
import { useLocalForage } from "./localForageContext";

type MyType = {
  name: string;
};

type MyGroupType = {
  [key: string]: bookmarksInterface;
};
export const useGetDocFromLocalForage = (
  key: any
): [MyGroupType | undefined, any] => {
  const [data, setData] = useState();
  const [loading, setLoading] = useState(true);
  const store = useLocalForage();

  useEffect(() => {
    async function fetchData() {
      try {
        
        const response = await store.bookmarks.getItem(key);
        
        setData(response);
        setLoading(false);
      } catch (error) {
        
        setLoading(false);
      }
    }
    fetchData();
  }, [key, store.bookmarks]);

  return [data, loading];
};
