import React, { useState, useEffect } from "react";
import Button from "@material-ui/core/Button";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogTitle from "@material-ui/core/DialogTitle";
import { useSignInModal } from "../Context/SignInModal";
import firebase from "firebase/app";
import {
  FacebookLoginButton,
  GoogleLoginButton,
  GithubLoginButton,
  createButton,
} from "react-social-login-buttons";
import { makeStyles, useTheme } from "@material-ui/core/styles";
import Link from "@material-ui/core/Link";
import Terms from "../Terms";

export default function SignInModal() {
  const modal = useSignInModal();
  const theme = useTheme();
  const [open, setOpen] = useState(modal.isVisible);
  useEffect(() => {
    
    setOpen(modal.isVisible);
  }, [modal]);
  const useStyles = makeStyles({
    root: {
      color: theme.palette.type === "light" ? "black" : "white",
    },
  });
  const classes = useStyles();
  const handleClose = () => {
    setOpen(false);
    modal.reset();
  };

  return (
    <Dialog
      open={open}
      onClose={handleClose}
      disableBackdropClick={true}
      aria-labelledby="form-dialog-title"
    >
      <DialogContent>
        <div>
          <div id="sign-in-with-google">
            <GoogleLoginButton
              style={{ fontSize: 15 }}
              iconSize={22}
              text="Sign in with Google"
              size={40}
              onClick={() => {
                setOpen(false);
                setTimeout(() => {
                  try {
                    try {
                      const GoogleAuthProvider =
                        new firebase.auth.GoogleAuthProvider();
                      firebase.auth().signInWithPopup(GoogleAuthProvider);
                    } catch (err) {
                      alert("Error7... Please try again.");
                    }
                  } catch (err) {
                    alert("Error8... Please try again.");
                  }
                  modal.reset();
                }, 50);
              }}
            />
          </div>
          <div id="sign-in-with-github">
            <GithubLoginButton
              style={{ fontSize: 15 }}
              iconSize={22}
              text="Sign in with Github"
              size={40}
              onClick={() => {
                setOpen(false);
                setTimeout(() => {
                  try {
                    const GithubAuthProvider =
                      new firebase.auth.GithubAuthProvider();
                    firebase.auth().signInWithPopup(GithubAuthProvider);
                  } catch (err) {
                    alert("Error9... Please try again.");
                  }
                  modal.reset();
                }, 50);
              }}
            />
          </div>
          <div id="sign-in-with-facebook">
            <FacebookLoginButton
              style={{ fontSize: 15 }}
              iconSize={22}
              text="Sign in with Facebook"
              size={40}
              onClick={() => {
                setOpen(false);
                setTimeout(() => {
                  try {
                    const FacebookAuthProvider =
                      new firebase.auth.FacebookAuthProvider();
                    firebase.auth().signInWithPopup(FacebookAuthProvider);
                  } catch (err) {
                    alert("Error10... Please try again.");
                  }
                  modal.reset();
                }, 50);
              }}
            />
          </div>
          <div style={{ maxWidth: 200, fontSize: 10, padding: "0px 5px" }}>
            <Terms />
          </div>
        </div>
      </DialogContent>
      <DialogActions>
        <Button onClick={handleClose} classes={classes}>
          Cancel
        </Button>
      </DialogActions>
    </Dialog>
  );
}
