import React, { useContext, useState } from "react";

const AddCardContext = React.createContext();

export const useAddCard = () => {
  return useContext(AddCardContext);
};

export const AddCardProvider = ({ children }) => {
  const [state, setState] = useState({ card: {}, stackOfIds: [] });

  const add = (card, stackOfIds) => setState({ card, stackOfIds });

  const reset = () => {
    setState({ card: {}, stackOfIds: [] });
  };

  return (
    <AddCardContext.Provider
      value={{ card: state.card, stackOfIds: state.stackOfIds, add: add, reset: reset }}
    >
      {children}
    </AddCardContext.Provider>
  );
};
