import React from "react";
import Toolbar from "@material-ui/core/Toolbar";
import { makeStyles } from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";
import { MoveOnScrollTopAppBar } from "../../General/Navigation/MoveOnScrollTopAppBar";
import { Right } from "../../General/Navigation/TopAppBar/Right";
import { useHistory } from "react-router-dom";
import ArrowBackIcon from "@material-ui/icons/ArrowBack";
import IconButton from "@material-ui/core/IconButton";

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
  title: {
    flexGrow: 1,
    display: "flex",
    overflow: "hidden",
  },
}));

export function TopAppBar(props) {
  const classes = useStyles();
  const history = useHistory();

  return (
    <MoveOnScrollTopAppBar>
      <Toolbar>
        <IconButton
          edge="start"
          onClick={() => {
            history.push("/");
          }}
        >
          <ArrowBackIcon />
        </IconButton>
        <Typography variant="h6" className={classes.title}>
          {props.youtubeData && (
            <>
              <span
                style={{
                  lineHeight: "normal",
                  display: "inline-flex",
                  alignItems: "center",
                  justifyContent: "left",
                  whiteSpace: "nowrap",
                  overflow: "hidden",
                }}
              >
                {props.youtubeData.title}
              </span>
              {props.bookmarkIcon}
            </>
          )}
        </Typography>

        <Right />
      </Toolbar>
    </MoveOnScrollTopAppBar>
  );
}
