import React, { useContext, useState } from "react";

const GithubStarsContext = React.createContext();

export const useGithubStars = () => {
  return useContext(GithubStarsContext);
};

export const GithubStarsProvider = ({ children }) => {
  const [stars, setStars] = useState(0);

  const set = (stars) => setStars(stars);

  return (
    <GithubStarsContext.Provider value={{ stars, set }}>
      {children}
    </GithubStarsContext.Provider>
  );
};
