import React, { useContext, useReducer } from "react";

const AddCardModalContext = React.createContext();

export const useAddCardModal = () => {
  return useContext(AddCardModalContext);
};

const SHOW_MODAL = "show";
const HIDE_MODAL = "hide";
const RESET = "reset";

const reducer = (state, action) => {
  switch (action.type) {
    case SHOW_MODAL: {
      return {
        isVisible: true,
        stackOfIds: action.stackOfIds,
      };
    }
    case HIDE_MODAL: {
      return { ...state, isVisible: false };
    }
    case RESET: {
      return {
        isVisible: false,
        stackOfIds: [],
      };
    }

    default:
      return state;
  }
};

export const AddCardModalProvider = ({ children }) => {
  const [state, dispatch] = useReducer(reducer, {
    isVisible: false,
    stackOfIds: [],
  });

  const show = (stackOfIds) =>
    dispatch({
      type: SHOW_MODAL,
      stackOfIds,
    });

  const hide = () => dispatch({ type: HIDE_MODAL });

  const reset = () => dispatch({ type: RESET });

  return (
    <AddCardModalContext.Provider
      value={{
        isVisible: state.isVisible,
        stackOfIds: state.stackOfIds,
        show,
        hide,
        reset,
      }}
    >
      {children}
    </AddCardModalContext.Provider>
  );
};
