import { useEffect } from "react";
import { useFireBaseAuth } from "../General/Autorization/Context/FireBase";
import { useLocalForage } from "./localForageContext";
import firebase from "firebase/app";
import "firebase/firestore";
import "firebase/auth";
import { useStoreUserCollectionToServer } from "./Context/StoreUserCollectionToServer";
import { useMergeCollections } from "./Context/MergeCollectionsContext";
import { useStoreBookmarksToServer } from "./Context/StoreBookmarksToServer";
import { usePrevious } from "../General/usePrevious";
import moment from "moment";
import pako from "pako";
import { useStoreBookmarksStateToServer } from "./Context/StoreBookmarksStatToServer";

const addIntersectionsWithLocal = (collection, localUserCollection) => {
  
  
  if (undefined !== collection && localUserCollection) {
    Object.entries(collection).forEach(([k, collectionValue]) => {
      Object.entries(localUserCollection).forEach(
        ([k, localUserCollectionValue]) => {
          
          

          if (
            null !== localUserCollectionValue &&
            null !== collectionValue &&
            undefined !== collectionValue.title &&
            collectionValue.title === localUserCollectionValue.title &&
            undefined === collectionValue.id
          ) {
            addIntersectionsWithLocal(
              collectionValue.children,
              localUserCollectionValue.children
            );
          } else if (
            null !== collectionValue &&
            null !== localUserCollectionValue &&
            undefined !== collectionValue.title &&
            undefined !== localUserCollectionValue.id
          ) {
            const found = collection.find(
              (card) => card.id === localUserCollectionValue.id
            );
            
            if (undefined === found) {
              collection.push(localUserCollectionValue);
            }
          }
        }
      );
    });
  }
};

function Uint8ToString(u8a) {
  var CHUNK_SZ = 0x8000;
  var c = [];
  for (var i = 0; i < u8a.length; i += CHUNK_SZ) {
    c.push(String.fromCharCode.apply(null, u8a.subarray(i, i + CHUNK_SZ)));
  }
  return c.join("");
}

export const RemoteStorage = () => {
  
  const fireBaseAuth = useFireBaseAuth();
  const storeUserCollectionToServer = useStoreUserCollectionToServer();
  const storeBookmarksToServer = useStoreBookmarksToServer();
  const storeBookmarksStateToServer = useStoreBookmarksStateToServer() 

  const store = useLocalForage();
  const mergeCollections = useMergeCollections();
  const prev = usePrevious({
    storeUserCollectionToServer,
    storeBookmarksToServer,
    fireBaseAuthIsSignIn: fireBaseAuth.isSignIn,
    fireBaseAuthUid: fireBaseAuth.uid,
    storeUserCollection: store.userCollection,
  });

  useEffect(() => {
    if (!fireBaseAuth.isSignIn || !prev) {
      return;
    }

    const db = firebase.firestore();
    const remoteUserCollection = db
      .collection("users")
      .doc(fireBaseAuth.uid)
      .collection("collection")
      .doc("doc");

    const remoteBookmarks = db
      .collection("users")
      .doc(fireBaseAuth.uid)
      .collection("bookmarks")
      .doc("doc");

    const remoteBackups = db
      .collection("users")
      .doc(fireBaseAuth.uid)
      .collection("backups");

    const increment = firebase.firestore.FieldValue.increment(1);
    const decrement = firebase.firestore.FieldValue.increment(-1);

    const remoteBookmarksStat = db.collection("bookmarks").doc("stat");

    const batch = db.batch();

    if (storeUserCollectionToServer !== prev.storeUserCollectionToServer) {
      

      store.userCollection.getItem("doc").then(function (result) {
        if (result) {
          //
          //
          //

          try {
            
            remoteUserCollection
              .set({
                data: Uint8ToString(pako.deflate(JSON.stringify(result))),
              })
              .then(() => {
                
              });
          } catch (e) {
            
          }
        }
      });
    } else if (fireBaseAuth.isSignIn !== prev.fireBaseAuthIsSignIn) {
      

      store.userCollection.getItem("doc").then(function (result) {
        if (!result) {
          result = []
        }
        remoteUserCollection.get().then((data) => {
          let returnedTarget;
          let remoteData = [];
          try {
            remoteData = JSON.parse(
              pako.inflate(
                Uint8Array.from(
                  [...data.data().data].map((ch) => ch.charCodeAt())
                ),
                { to: "string" }
              )
            );
          } catch (error) {
            
          }

          let clone = [...result];
          if (result) {
            returnedTarget = Object.assign(result, remoteData);
          } else {
            returnedTarget = remoteData;
          }

          
          addIntersectionsWithLocal(returnedTarget, clone);

          

          try {
            
            remoteUserCollection
              .set({
                data: Uint8ToString(
                  pako.deflate(JSON.stringify(returnedTarget))
                ),
              })
              .then(() => {
                
              });
          } catch (e) {
            
          }

          remoteBackups
            .doc("list")
            .get()
            .then((list) => {
              let data = list.data();

              let currentTime = new Date();
              let dateTimeUnix = moment(currentTime).unix();
              let dateUnix = moment(currentTime.toDateString()).unix();

              

              if (!data || !(dateUnix in data)) {
                remoteBackups.doc(dateUnix.toString()).set({
                  data: Uint8ToString(pako.deflate(JSON.stringify(result))),
                });

                let newObj = Object.assign(
                  {
                    [dateUnix]: { time: dateTimeUnix },
                  },
                  data
                );
                
                remoteBackups.doc("list").set(newObj);
              }
            });

          
          store.userCollection.setItem("doc", returnedTarget).then(function () {
            mergeCollections.update();
          });
        });
      });
    }

    if (
      storeBookmarksToServer !== prev.storeBookmarksToServer &&
      storeBookmarksToServer.collection !== null
    ) {
      
      store.bookmarks.getItem("doc").then(function (result) {
        

        if (result) {

           db.runTransaction(async (t) => {
            t.set(remoteBookmarks, { data: JSON.stringify(result) });
            
            const collectionName = storeBookmarksToServer.collection.replace(
            /[~\*\/\[\]]/g,
            "#"
            );
            if (storeBookmarksToServer.action === "store") {
              
              t.update(remoteBookmarksStat, { [collectionName]: increment });
            } else if (storeBookmarksToServer.action === "remove") {
              
              t.update(remoteBookmarksStat, { [collectionName]: decrement });
            }
          }).then(()=>{
            storeBookmarksStateToServer.store() 
          });

          //batch.set(remoteBookmarks, { data: JSON.stringify(result) });
          //remoteBookmarks.set({ data: JSON.stringify(result) });

/*           if (storeBookmarksToServer.action === "store") {
            
            batch.update(remoteBookmarksStat, { [collectionName]: increment });
          } else if (storeBookmarksToServer.action === "remove") {
            
            batch.update(remoteBookmarksStat, { [collectionName]: decrement });
          } */
          //batch.commit();
        }
      });
    } else if (fireBaseAuth.isSignIn !== prev.fireBaseAuthIsSignIn) {
      store.bookmarks.getItem("doc").then(function (result) {
        
        remoteBookmarks.get().then((data) => {
          let remoteData = [];
          if (data.data() !== undefined) {
            remoteData = JSON.parse(data.data().data);
          }

          let returnedTarget;
          if (result) {
            returnedTarget = Object.assign(result, remoteData);
          } else {
            returnedTarget = remoteData;
          }

          remoteBookmarks.set({ data: JSON.stringify(returnedTarget) });

          store.bookmarks.setItem("doc", returnedTarget).then(function () {
            mergeCollections.update();
          });
        });
      });
    }
  }, [
    storeUserCollectionToServer,
    storeBookmarksToServer,
    fireBaseAuth.isSignIn,
    fireBaseAuth.uid,
    store.userCollection,
    store.bookmarks,
  ]);

  return <></>;
};
