import { bookmarksInterface } from "./bookmarksInterface";

export const addLocalBokmarks = (
  localData: any,
  collection: bookmarksInterface
) => {
  if (localData && Object.keys(localData!).length > 0) {
    Object.entries(collection).forEach(([k, v]) => {
      if (undefined !== v.url) {
        if (undefined !== localData[v.url!]) {
          collection[k].local = true;
        }
      }
    });
  }
};
