export const deleteCardService = (arr, stack, id) => {
  let newArr = arr[stack[0]];
  if (undefined !== newArr && undefined !== newArr.children) {
    let newStack = stack.slice(1);
    if (undefined !== newStack) {
      if (stack.length === 1) {
        const index = newArr.children.findIndex((o) => o && id && o.id === id);
        //delete newArr.children[index];
        if (process.env.REACT_APP_DEBUG) console.log('index', index, newArr.children);
        newArr.children[index].saved = false;
        newArr.children = newArr.children.filter(function (el) {
          return el != null;
        });
        console.log("deleteCardService", newArr.children[index], stack);
      } else {
        deleteCardService(newArr.children, stack.slice(1), id);
      }
    }
  }
};
