import { useState, useEffect, useRef } from "react";
import AppBar from "@material-ui/core/AppBar";
import { createMuiTheme, ThemeProvider } from "@material-ui/core/styles";
import { Tabs } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import { useHistory } from "react-router-dom";
import { useWheelDirection } from "../Context/WheelDirectionContext";
import { useLocation } from "react-router-dom";
import { useTheme } from "@material-ui/core/styles";
import { TopTabsMenuSkeleton } from "../TopTabsMenuSkeleton";
import { usePrevious } from "../usePrevious";
import { useComponentSizes } from "../Context/ComponentSizesContext";
import { useShowSignInBar } from "../Context/ShowSignInBarContext";
import { useHashInInitialUrl } from "../Context/HashInInitialUrlContext";
import { useHashChange } from "../Context/HashChangeContext";
const pages = ["/", "/browse", "/bookmarks"];

export function MoveOnScrollTopTabsMenu(props) {
  const wheelDirection = useWheelDirection();
  const location = useLocation();
  const prevScrollTrigger = useRef(true);

  const showSignInBar = useShowSignInBar();
  const componentSizes = useComponentSizes();

  const hashInInitialUrl = useHashInInitialUrl();
  const hashChange = useHashChange();
  const [active, setActive] = useState(location.pathname);

  const history = useHistory();

  const prev = usePrevious({
    active,
  });

  const useTabStyles = makeStyles({
    root: {
      justifyContent: "center",
    },
    scroller: {
      flexGrow: "0",
    },
  });
  const handleChange = (newValue) => {
    

    setActive(newValue);

    history.push(newValue);
  };

  useEffect(() => {
    hashChange.setHash("");
    hashInInitialUrl.reset();

    if (prev) {
      return;
    }
    

    if (undefined !== pages.find((element) => element === active)) {
      return;
    } else if (active === "/backups") {
      

      setActive(false);
    } else {
      setActive("/browse");
    }
  }, [active]);

  useEffect(() => {
    prevScrollTrigger.current = wheelDirection.direction;
  }, [wheelDirection.direction]);

  const theme = useTheme();

  const themeNew = createMuiTheme({
    palette: theme.palette,
    overrides: {
      // Style sheet name 
      MuiPaper: {
        // Name of the rule
        elevation4: {
          // Some CSS
          boxShadow: "",
          clipPath: "inset(0px 0px -10px)",
        },
      },
    },
  });
  

  const [Styles, setStyles] = useState({
    position: "sticky",
    transform: "translateY(0px)",
  });

  const classes = useTabStyles();
  const useStyles = makeStyles({
    colorPrimary: {
      backgroundColor: theme.palette.background.default,
    },
  });

  useEffect(() => {
    setStyles((prev) => ({
      ...prev,
      transform:
        "translateY" +
        (showSignInBar.status === false
          ? 0
          : componentSizes.topSignInAppBarHeight) +
        "px)",
    }));
  }, [componentSizes.topSignInAppBarHeight, showSignInBar.status]);

  const classes0 = useStyles();

  if (
    undefined === pages.find((element) => element === active) &&
    active !== false
  ) {
    

    return <TopTabsMenuSkeleton />;
  }

  return (
    <>
      <div>
        <ThemeProvider theme={themeNew}>
          <AppBar
            style={Styles}
            color="inherit"
            className={classes0.colorPrimary}
          >
            <Tabs
              classes={{ root: classes.root, scroller: classes.scroller }}
              onChange={(event, newValue) => {
                handleChange(newValue);
              }}
              value={active}
              indicatorColor="primary"
              textColor="primary"
              variant={"scrollable"}
              scrollButtons={"on"}
            >
              {props.children}
            </Tabs>
          </AppBar>
        </ThemeProvider>
        <div style={{ display: "none" }}></div>
      </div>
    </>
  );
}
