import React from "react";
import MenuItem from "@material-ui/core/MenuItem";
import FormControl from "@material-ui/core/FormControl";
import Select from "@material-ui/core/Select";
import { useMoveCard } from "./Context/MoveCard";

export function SelectCategory(props) {
  
  const moveCard = useMoveCard();
  const handleChange = (e) => {
    let data = e.target.value.split("/");
    let toStackOfIds = data[0].split("-");
    let toCategoryTitle;
    if (toStackOfIds.length > 1) {
      toCategoryTitle = data[1];
    }
    

    

    if (toStackOfIds.length > props.fromStackOfIds.length) {
      let partForCompare = toStackOfIds.slice(0, props.fromStackOfIds.length);

      const eq = partForCompare.every(
        (value, index) => Number(value) === Number(props.fromStackOfIds[index])
      );
      if (eq) {
        toStackOfIds[props.fromStackOfIds.length] =
          Number(toStackOfIds[props.fromStackOfIds.length]) - 1;
      }

      
      data[0] = toStackOfIds.join("-");
    }

    moveCard.move(props.card, props.fromStackOfIds, data[0], toCategoryTitle);
    props.closeModal();
    /* props.moveCardHandler(
      props.card,
      props.cardId,
      props.fromStackOfIds,
      e.target.value
    );
    props.closeModal(); */
    //props.delCardHandler(e.target.value.cardId, e.target.value.fromStackOfIds);
  };

  return (
    <FormControl style={{ width: "100%" }}>
      <Select
        id={"move-card-select-" + props.fromStackOfIds.join("-")}
        value={props.fromStackOfIds.join("-") + "/" + props.categoryTitle}
        onChange={handleChange}
        style={{ position: "static" }}
      >
        {props.cats.map((cat) => {
          return (
            <MenuItem
              id={"move-card-option-" + cat.stack.join("-")}
              style={{
                paddingLeft: cat.spaces * 10 + 10 + "px",
              }}
              value={cat.stack.join("-") + "/" + cat.text.trim()}
            >
              {cat.text}
            </MenuItem>
          );
        })}
      </Select>
    </FormControl>
  );
}
