import React, { useContext, useReducer, useState } from "react";

const StoreBookmarksToServerContext = React.createContext();

export const useStoreBookmarksToServer = () => {
  return useContext(StoreBookmarksToServerContext);
};

const STORE = "STORE";
const REMOVE = "REMOVE";

const reducer = (state, action) => {
  switch (action.type) {
    case STORE:
      return { ...state, type: "store", collection: action.collection };
    case REMOVE:
      return { ...state, type: "remove", collection: action.collection };
    default:
      return state;
  }
};

export const StoreBookmarksToServerProvider = ({ children }) => {
  const [state, dispatch] = useReducer(reducer, {
    collection: null,
    type: null,
  });

  const store = (collection) => {
    dispatch({ type: STORE, collection });
  };

  const remove = (collection) => {
    dispatch({ type: REMOVE, collection });
  };

  return (
    <StoreBookmarksToServerContext.Provider
      value={{
        store,
        remove,
        action: state.type,
        collection: state.collection,
      }}
    >
      {children}
    </StoreBookmarksToServerContext.Provider>
  );
};
