const MyCollection = lazy(
  () =>
    new Promise(async (resolve) => {
      const module = await import("./MyCollection");
      NUMBER_OF_LOADED_COMPONENTS.inc();
      resolve(module);
    })
);
const KitsList = lazy(
  () =>
    new Promise(async (resolve) => {
      const module = await import("./Awesome/KitsList");
      NUMBER_OF_LOADED_COMPONENTS.inc();
      resolve(module);
    })
);
const AwesomeSelector = lazy(
  () =>
    new Promise(async (resolve) => {
      const module = await import("./Awesome/Selector");
      NUMBER_OF_LOADED_COMPONENTS.inc();
      resolve(module);
    })
);

const disableRipple = {
  // Name of the component
  MuiButtonBase: {
    // The properties to apply
    disableRipple: true, // No more ripple, on the whole application!
  },
};

const MuiFormLabel = {
  MuiFormLabel: {
    asterisk: {
      color: "#db3131",
      "&$error": {
        color: "#db3131",
      },
    },
  },
};

const themeTypographyH5 = {
  fontSize: "0.6rem",

  "@media (min-width:420px)": {
    fontSize: "0.7rem",
  },
  "@media (min-width:445px)": {
    fontSize: "0.8rem",
  },
  "@media (min-width:500px)": {
    fontSize: "1rem",
  },
};

const themeLight = {
  overrides: MuiFormLabel,
  palette: {
    background: {
      default: "#fff",
      paper: "#fff",
    },
    primary: {
      contrastText: "#000",
      main: "#fff",
    },
    type: "light",
  },
  props: disableRipple,
  typography: { h5: themeTypographyH5 },
};

const themeDark = {
  overrides: MuiFormLabel,
  palette: {
    background: {
      default: "#12100e",
      paper: "#1c1a18",
    },
    type: "dark",
    primary: {
      contrastText: "#fff",
      main: "#8182bd",
    },
    secondary: {
      //main: "#dc834b",
      main: "#cf72e2",
    },
  },
  props: disableRipple,
  typography: { h5: themeTypographyH5 },
};

export default function Main() {
  const store = useLocalForage();
  const [theme, setTheme] = useState(
    window.matchMedia &&
      window.matchMedia("(prefers-color-scheme: dark)").matches
      ? themeDark
      : themeLight
  );

  useEffect(() => {
    store.settings.getItem("doc").then(function (results) {
      if (!results) {
        if (
          window.matchMedia &&
          window.matchMedia("(prefers-color-scheme: dark)").matches
        ) {
          setTheme(themeDark);
        } else {
          setTheme(themeLight);
        }
      } else {
        console.log("results.theme:", results.theme);
        if (results.theme === "light") {
          setTheme(themeLight);
        } else {
          setTheme(themeDark);
        }
      }
    });
  }, [store.settings]);

  const toggleDarkTheme = () => {
    setTheme((prev) => {
      if (prev.palette.type === "light") {
        store.settings.setItem("doc", { theme: "dark" });
        return themeDark;
      }
      store.settings.setItem("doc", { theme: "light" });
      return themeLight;
    });
  };
  const muiTheme = createMuiTheme(theme);

  return (
    <MuiThemeProvider theme={muiTheme}>
      <CssBaseline />
      <SignInModalProvider>
        <ShowSignInBarProvider>
          <MergeCollectionsProvider>
            <StoreUserCollectionToServerProvider>
              <DrawerWithMenuProvider>
                <DrawerProvider>
                  <AlertProvider>
                    <EditCategoryProvider>
                      <EditCategoryModalProvider>
                        <DeleteCategoryProvider>
                          <DeleteCategoryModalProvider>
                            <AddCategoryProvider>
                              <AddCategoryModalProvider>
                                <DeleteCardProvider>
                                  <MoveCardProvider>
                                    <SaveCardProvider>
                                      <AddCardProvider>
                                        <MoveCardModalProvider>
                                          <EditCardModalProvider>
                                            <AddCardModalProvider>
                                              <VideoPlayerProvider>
                                                <GithubStarsProvider>
                                                  <ProductHuntVotesProvider>
                                                    <SkeletonLoaderForHashNavigationProvider>
                                                      <StoreVotingStatsToServerProvider>
                                                        <StoreBookmarksToServerProvider>
                                                          <StoreBookmarksStateToServerProvider>
                                                            <VideoProgressBarProvider>
                                                              {/* <FirestoreProvider
                                          firebase={firebase}
                                          {...config}
                                        > */}
                                                              <style>
                                                                {styles2}
                                                              </style>

                                                              <BrowserRouter>
                                                                <>
                                                                  <ScrollToTop>
                                                                    <Switch>
                                                                      <Route
                                                                        exact
                                                                        path="/y/:youtubeId"
                                                                      >
                                                                        <YoutubeConverter />
                                                                      </Route>
                                                                      <Route
                                                                        exact
                                                                        path="/:user/:kit"
                                                                      >
                                                                        <CollectionList
                                                                          theme={
                                                                            theme
                                                                          }
                                                                        />
                                                                      </Route>
                                                                      <Route path="/:user([\w\-]+)/:kitList([\w\-\/]+)?/:kit([\w\-]+)/:collectionId([\w\-]+)">
                                                                        {console.log(
                                                                          "Main",
                                                                          new Date().toLocaleString()
                                                                        )}
                                                                        <AwesomeSelector />
                                                                      </Route>
                                                                      <Route
                                                                        exact
                                                                        path="/"
                                                                      >
                                                                        <Home />
                                                                      </Route>
                                                                      <Route
                                                                        exact
                                                                        path="/saved"
                                                                      >
                                                                        <MyCollection
                                                                          theme={
                                                                            theme
                                                                          }
                                                                        />
                                                                      </Route>
                                                                      <Route
                                                                        exact
                                                                        path="/browse"
                                                                        children={
                                                                          <KitsList />
                                                                        }
                                                                      />
                                                                      <Route
                                                                        exact
                                                                        path="/shared"
                                                                        children={
                                                                          <Shared />
                                                                        }
                                                                      />
                                                                      <Route
                                                                        exact
                                                                        path="/shared/:id"
                                                                        children={
                                                                          <SharedCard />
                                                                        }
                                                                      />
                                                                      <Route
                                                                        exact
                                                                        path="/bookmarks"
                                                                        children={
                                                                          <Bookmarks />
                                                                        }
                                                                      />
                                                                      <Route
                                                                        exact
                                                                        path="/backups"
                                                                        children={
                                                                          <Backups />
                                                                        }
                                                                      />
                                                                      <Route
                                                                        exact
                                                                        path="/settings"
                                                                        children={
                                                                          <MySettings />
                                                                        }
                                                                      />
                                                                    </Switch>
                                                                  </ScrollToTop>
                                                                </>
                                                                <DrawerWithMenu
                                                                  toggleDarkTheme={
                                                                    toggleDarkTheme
                                                                  }
                                                                  theme={theme}
                                                                />
                                                              </BrowserRouter>

                                                              <UpdateInterfaceBasedOnLocalData />
                                                              {/*  </FirestoreProvider> */}

                                                              <Alert />
                                                              <MoveCardModal />
                                                              <EditCardModal />
                                                              <AddCardModal />
                                                              <AddCategoryModal />
                                                              <DeleteCategoryModal />
                                                              <EditCategoryModal />
                                                              <SignInModal />
                                                              <TopSignInAppBar />

                                                              <RemoteStorage />
                                                              <AfterUpdate />
                                                            </VideoProgressBarProvider>
                                                          </StoreBookmarksStateToServerProvider>
                                                        </StoreBookmarksToServerProvider>
                                                      </StoreVotingStatsToServerProvider>
                                                    </SkeletonLoaderForHashNavigationProvider>
                                                  </ProductHuntVotesProvider>
                                                </GithubStarsProvider>
                                              </VideoPlayerProvider>
                                            </AddCardModalProvider>
                                          </EditCardModalProvider>
                                        </MoveCardModalProvider>
                                      </AddCardProvider>
                                    </SaveCardProvider>
                                  </MoveCardProvider>
                                </DeleteCardProvider>
                              </AddCategoryModalProvider>
                            </AddCategoryProvider>
                          </DeleteCategoryModalProvider>
                        </DeleteCategoryProvider>
                      </EditCategoryModalProvider>
                    </EditCategoryProvider>
                  </AlertProvider>
                </DrawerProvider>
              </DrawerWithMenuProvider>
            </StoreUserCollectionToServerProvider>
          </MergeCollectionsProvider>
        </ShowSignInBarProvider>
      </SignInModalProvider>
    </MuiThemeProvider>
  );
}
