import React, { useState, useEffect } from "react";
import Button from "@material-ui/core/Button";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogTitle from "@material-ui/core/DialogTitle";
import { useDeleteCategoryModal } from "../Context/DeleteCategoryModal";
import { useDeleteCategory } from "../Context/DeleteCategory";

export const DeleteCategoryModal = () => {
  const modal = useDeleteCategoryModal();
  const deleteCategory = useDeleteCategory();
  const [open, setOpen] = useState(false);
  useEffect(() => {
    setOpen(modal.isVisible);
  }, [modal]);

  const [inputValue, setInputValue] = useState("");

  useEffect(() => {
    setInputValue(modal.title);
  }, [modal]);

  const handleClose = () => {
    setOpen(false);
    modal.reset()
    //props.setOpenDelCategoryModal(false);
  };
  const handleDelete = () => {
    deleteCategory.delete(modal.stackOfIds);
    setOpen(false);
    modal.reset()
    //props.setOpenDelCategoryModal(false);
  };

  return (
      <Dialog
        id={"delete-category-modal"}
        open={open}
        onClose={handleClose}
        disableBackdropClick={true}
      >
        <DialogTitle>Delete category</DialogTitle>
        <DialogContent>
          <p>{inputValue}</p>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose} color="primary">
            Cancel
          </Button>
          <Button
            id="delete-category-button"
            onClick={handleDelete}
            color="primary"
          >
            Delete
          </Button>
        </DialogActions>
      </Dialog>
  );
};
