import { useEffect, useLayoutEffect, useState } from "react";
import { useHistory } from "react-router-dom";
import firebase from "firebase/app";
import "firebase/firestore";
import "firebase/auth";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import ListItemSecondaryAction from "@material-ui/core/ListItemSecondaryAction";
import ListItemText from "@material-ui/core/ListItemText";
import IconButton from "@material-ui/core/IconButton";
import SettingsBackupRestoreIcon from "@material-ui/icons/SettingsBackupRestore";
import { TopAppBar } from "./Navigation/TopAppBar";
import { TopTabsMenu } from "../General/Navigation/TopTabsMenu";
//const worker = new Worker("/thread.worker.js");
import { useLocalForage } from "../DataSource/localForageContext";
import moment from "moment";
import Container from "@material-ui/core/Container";
import { useTopTranformerTrigger } from "../General/Context/TopTranformerTriggerContext";
import { useWheelDirection } from "../General/Context/WheelDirectionContext";
import { TopTabsMenuSkeleton } from "../General/TopTabsMenuSkeleton";
import { CollectionLoader } from "../Loader/collection";
import { useGetRemoteBackups } from "../DataSource/getRemoteBackups";
import { useFireBaseAuth } from "../General/Autorization/Context/FireBase";
import GetAppIcon from "@material-ui/icons/GetApp";
import fileSaver from "file-saver";
import pako from "pako";
import { Paper } from "@material-ui/core";
import { config } from "../firebase.config";

export function Backups() {
  const [setedUp, setup] = useState(false);
  const store = useLocalForage();
  const [backups, setBackups] = useState([]);
  const [setedUpBookmarks, setUpBookmarks] = useState(false);
  const history = useHistory();
  const wheelDirection = useWheelDirection();
  const [remoteData, remoteLoading] = useGetRemoteBackups();
  const fireBaseAuth = useFireBaseAuth();
  const topTranformerTrigger = useTopTranformerTrigger();

  useEffect(() => {
    if (remoteLoading) {
      return;
    } else {
      
      const ordered = Object.entries(remoteData).sort().reverse();
      

      setBackups(ordered);
    }
  }, [remoteLoading]);

  useLayoutEffect(() => {
    topTranformerTrigger.reset();
    wheelDirection.setDirection("down");
  }, []);

  

  if (remoteLoading) {
    return (
      <>
        <TopAppBar />
        <CollectionLoader />
      </>
    );
  }

  const restore = (k) => {
    if (!firebase.apps.length) {
      firebase.initializeApp({ ...config });
    }
    const db = firebase.firestore();
    const remoteBackups = db
      .collection("users")
      .doc(fireBaseAuth.uid)
      .collection("backups");
    
    remoteBackups
      .doc(k.toString())
      .get()
      .then((data) => {
        let remoteData = JSON.parse(
          pako.inflate(
            Uint8Array.from([...data.data().data].map((ch) => ch.charCodeAt())),
            { to: "string" }
          )
        );

        store.userCollection.setItem("doc", remoteData).then(function () {
          history.push("/");
        });
      });
  };

  const download = (k, time) => {
    if (!firebase.apps.length) {
      firebase.initializeApp({ ...config });
    }
    const db = firebase.firestore();
    const remoteBackups = db
      .collection("users")
      .doc(fireBaseAuth.uid)
      .collection("backups");
    
    remoteBackups
      .doc(k.toString())
      .get()
      .then((data) => {
        let remoteData = JSON.parse(
          pako.inflate(
            Uint8Array.from([...data.data().data].map((ch) => ch.charCodeAt())),
            { to: "string" }
          )
        );

        var blob = new Blob([JSON.stringify(remoteData, null, 2)], {
          type: "text/plain;charset=utf-8",
        });
        fileSaver.saveAs(blob, time + "-collection.json");
      });
  };

  return (
    <>
      <TopAppBar />
      <Container
        maxWidth="sm"
        style={{
          paddingLeft: 8,
          paddingRight: 8,
          display: "flex",
          flexDirection: "column",
          marginTop: 10,
        }}
      >
        <Paper variant="outlined">
          <List>
            {backups.map((k, v) => (
              <ListItem>
                <ListItemText
                  primary={moment.unix(k[1].time).format("YYYY-MM-DD HH:mm:ss")}
                />
                <ListItemSecondaryAction>
                  <IconButton edge="end" onClick={() => restore(k[0])}>
                    <SettingsBackupRestoreIcon />
                  </IconButton>
                  <IconButton
                    edge="end"
                    onClick={() =>
                      download(
                        k[0],
                        moment.unix(k[1].time).format("YYYY-MM-DD-HH-mm")
                      )
                    }
                  >
                    <GetAppIcon />
                  </IconButton>
                </ListItemSecondaryAction>
              </ListItem>
            ))}
          </List>
        </Paper>
      </Container>
    </>
  );
}
