import React, { useContext, useState } from "react";

const StoreBookmarksStateToServerContext = React.createContext();

export const useStoreBookmarksStateToServer = () => {
  return useContext(StoreBookmarksStateToServerContext);
};

export const StoreBookmarksStateToServerProvider = ({ children }) => {
  const [update, setUpdate] = useState();

  const store = () => {
    setUpdate({ true: true });
  };

  return (
    <StoreBookmarksStateToServerContext.Provider value={{ store }}>
      {children}
    </StoreBookmarksStateToServerContext.Provider>
  );
};
