import React, { useState, useEffect, useRef } from "react";
import Button from "@material-ui/core/Button";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogTitle from "@material-ui/core/DialogTitle";
import TextField from "@material-ui/core/TextField";
import { useEditCardModal } from "../Context/EditCardModal";
import { useSaveCard } from "../Context/SaveCard";
import DialogContent from "@material-ui/core/DialogContent";

export default function EditCardModal() {
  const modal = useEditCardModal();
  const saveCard = useSaveCard();
  const [open, setOpen] = useState(false);
  const [inputValue, setInputValue] = useState("");
  useEffect(() => {
    setOpen(modal.isVisible);
  }, [modal]);

  useEffect(() => {
    setInputValue(modal.card);
  }, [modal]);

  const handleClose = () => {
    setOpen(false);
    modal.reset() 
    //props.setOpenEditCardModal(false);
  };
  const handleSave = () => {
    const card = {
      id: modal.card.id,
      title: titleRef.current.value,
      description: descriptionRef.current.value,
      url: urlRef.current.value,
      ava: avaRef.current.value,
      license: licenseRef.current.value,
      upd: updRef.current.value,
      created: createdRef.current.value,
      stars: starsRef.current.value,
    };
    /* props.editCardHandler(
      card,
      props.openEditCardModalParams.cardId,
      props.openEditCardModalParams.StackOfIds
    ); */

    saveCard.save(card, modal.stackOfIds);
    setOpen(false);
    modal.reset()
    //props.setOpenEditCardModal(false);
  };

  const avaRef = useRef();
  const titleRef = useRef();
  const descriptionRef = useRef();
  const urlRef = useRef();
  const fullDescriptionRef = useRef();
  const licenseRef = useRef();
  const updRef = useRef();
  const createdRef = useRef();
  const starsRef = useRef();

  const onChangeHandler = (event) => {
    setInputValue(event.target.value);
  };
  /* useEffect(() => {
    if (props.open === true) {
      setOpen(true);
    } else if (props.open === false) {
      setOpen(false);
    }
  }, [props.open]); */

  return (
      <Dialog
        id={"edit-card-modal"}
        open={open}
        onClose={handleClose}
        disableBackdropClick={true}
        aria-labelledby="form-dialog-title"
      >
        <DialogTitle id="form-dialog-title">Edit Card</DialogTitle>
        <DialogContent>
            <TextField
              required
              autoFocus
              id="stars"
              label="stars"
              fullWidth
              inputRef={starsRef}
              onChange={onChangeHandler}
              value={undefined !== inputValue ? inputValue.stars : ""}
            />
            <TextField
              required
              autoFocus
              id="upd"
              label="upd"
              fullWidth
              inputRef={updRef}
              onChange={onChangeHandler}
              value={undefined !== inputValue ? inputValue.upd : ""}
            />
          <TextField
            required
            autoFocus
            id="created"
            label="created"
            fullWidth
            inputRef={createdRef}
            onChange={onChangeHandler}
            value={undefined !== inputValue ? inputValue.created : ""}
          />
          <TextField
            required
            autoFocus
            id="license"
            label="license"
            fullWidth
            inputRef={licenseRef}
            onChange={onChangeHandler}
            value={undefined !== inputValue ? inputValue.license : ""}
          />
          <TextField
            required
            autoFocus
            id="ava"
            label="Ava"
            fullWidth
            inputRef={avaRef}
            onChange={onChangeHandler}
            value={undefined !== inputValue ? inputValue.ava : ""}
          />
          <TextField
            required
            autoFocus
            id="name"
            label="Title"
            fullWidth
            inputRef={titleRef}
            onChange={onChangeHandler}
            value={undefined !== inputValue ? inputValue.t : ""}
          />
          <TextField
            required
            autoFocus
            multiline
            rows={4}
            id="name"
            label="Description"
            fullWidth
            inputRef={descriptionRef}
            onChange={onChangeHandler}
            value={undefined !== inputValue ? inputValue.description : ""}
          />
          <TextField
            required
            autoFocus
            id="name"
            label="Url"
            fullWidth
            inputRef={urlRef}
            onChange={onChangeHandler}
            value={undefined !== inputValue ? inputValue.url : ""}
          />
          <TextField
            required
            autoFocus
            multiline
            rows={4}
            id="name"
            label="Full Description"
            fullWidth
            inputRef={fullDescriptionRef}
            onChange={onChangeHandler}
            value={undefined !== inputValue ? inputValue.fullDescription : ""}
          />
          <DialogActions>
            <Button onClick={handleClose} color="primary">
              Cancel
            </Button>
            <Button id="save-card-button" onClick={handleSave} color="primary">
              save
            </Button>
          </DialogActions>
        </DialogContent>
      </Dialog>
  );
}
