import IDataJson from "../Interfaces/IDataJson";

export const findStacksOfIdsForCardsCollections = (
    localData: IDataJson[],
    stackOfLocalIds: any[],
    stacksOfLocalIds: any
  ): any => {
    if (process.env.REACT_APP_DEBUG)
      console.log("findStackOfIdsForCardsCollection", localData, stackOfLocalIds);
    Object.entries(localData).forEach(([k, v]) => {
      //Object.entries(localData).forEach(([k, v]) => {
      if (process.env.REACT_APP_DEBUG) console.log("k,v", k, v);
      let newLocalStackOfIds: any[] = [];
      Array.prototype.push.apply(newLocalStackOfIds, stackOfLocalIds);
      Array.prototype.push.apply(newLocalStackOfIds, [k]);
  
      if (v.id) {
        stacksOfLocalIds[v.id] = newLocalStackOfIds.slice(0, -1);
      } else if (v.children) {
        findStacksOfIdsForCardsCollections(
          v.children,
          newLocalStackOfIds,
          stacksOfLocalIds
        );
      }
    });
  };
  