import React from "react";
export function HorizontalCenterIconAndText(props) {
  const style = {
    display: "flex",
    alignItems: props.alignItems ?? "center",
    flexWrap: "nowrap",
    marginRight: props.marginRight ?? 0,
    marginBottom: props.marginBottom ?? 0,
  };
  let onClick = {};
  if (undefined !== props.url) {
    style.cursor = "pointer";
    onClick = { onClick: () => window.open(props.url, "_blank") };
  }
  return (
    <div style={{ display: "flex" }}>
      <div style={style} {...onClick}>
        {props.children}
      </div>
    </div>
  );
}
