import React, { useContext, useState } from "react";

const StoreUserCollectionToServerContext = React.createContext();

export const useStoreUserCollectionToServer = () => {
  return useContext(StoreUserCollectionToServerContext);
};

export const StoreUserCollectionToServerProvider = ({ children }) => {
  const [update, setUpdate] = useState(0);

  const store = () => {
    setUpdate({ true: true });
  };

  return (
    <StoreUserCollectionToServerContext.Provider value={{ store }}>
      {children}
    </StoreUserCollectionToServerContext.Provider>
  );
};
