import React, { useState, useEffect } from "react";
import Button from "@material-ui/core/Button";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogTitle from "@material-ui/core/DialogTitle";
import { SelectCategory } from "../SelectCategory";
import { useMoveCardModal } from "../Context/MoveCardModal";

export default function MoveCardModal() {
  const modal = useMoveCardModal();
  const [open, setOpen] = useState(modal.isVisible);
  useEffect(() => {
    setOpen(modal.isVisible);
  }, [modal]);

  const handleClose = () => {
    setOpen(false)
    modal.reset()
  };

  return (
      <Dialog
        id={"move-card-modal"}
        open={open}
        onClose={handleClose}
        disableBackdropClick={true}
        aria-labelledby="form-dialog-title"
      >
        <DialogTitle id="form-dialog-title">Move Card</DialogTitle>
        <DialogContent>
          <SelectCategory
            fromStackOfIds={modal.stackOfIds}
            card={modal.card}
            cardId={modal.cardId}
            categoryTitle={modal.categoryTitle}
            cats={modal.cats}
            //moveCardHandler={props.moveCardHandler}
            closeModal={handleClose}
          />
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose} color="primary">
            Cancel
          </Button>
        </DialogActions>
      </Dialog>
  );
}
