import React, { useContext, useReducer } from "react";

const SignInModalContext = React.createContext();

export const useSignInModal = () => {
  return useContext(SignInModalContext);
};

const SHOW_MODAL = "show";
const HIDE_MODAL = "hide";
const RESET = "reset";

const reducer = (state, action) => {
  switch (action.type) {
    case SHOW_MODAL: {
      

      return { isVisible: true };
    }
    case HIDE_MODAL: {
      return { ...state, isVisible: false };
    }

    case RESET: {
      return { isVisible: false };
    }

    default:
      return state;
  }
};

export const SignInModalProvider = ({ children }) => {
  const [state, dispatch] = useReducer(reducer, { isVisible: false });

  const show = () => dispatch({ type: SHOW_MODAL });
  const hide = () => dispatch({ type: HIDE_MODAL });
  const reset = () => dispatch({ type: RESET });

  return (
    <SignInModalContext.Provider
      value={{
        isVisible: state.isVisible,
        show,
        hide,
        reset,
      }}
    >
      {children}
    </SignInModalContext.Provider>
  );
};
