import React, { useContext, useState } from "react";

const MergeCollectionsContext = React.createContext();

export const useMergeCollections = () => {
  return useContext(MergeCollectionsContext);
};

export const MergeCollectionsProvider = ({ children }) => {
  const [counter, setCounter] = useState(0);

  const update = () => {
    
    setCounter(counter + 1);
  };

  return (
    <MergeCollectionsContext.Provider value={{ counter, update }}>
      {children}
    </MergeCollectionsContext.Provider>
  );
};
