import React, { useContext, useReducer } from "react";

const EditCategoryModalContext = React.createContext();

export const useEditCategoryModal = () => {
  return useContext(EditCategoryModalContext);
};

const SHOW_MODAL = "show";
const HIDE_MODAL = "hide";
const RESET = "reset";

const reducer = (state, action) => {
  switch (action.type) {
    case SHOW_MODAL: {
      return {
        isVisible: true,
        stackOfIds: action.stackOfIds,
        currentCategoryName: action.currentCategoryName,
      };
    }
    case HIDE_MODAL: {
      return { ...state, isVisible: false };
    }
    case RESET: {
      return {
        isVisible: false,
        stackOfIds: [],
        currentCategoryName: "",
      };
    }

    default:
      return state;
  }
};

export const EditCategoryModalProvider = ({ children }) => {
  const [state, dispatch] = useReducer(reducer, {
    isVisible: false,
    stackOfIds: [],
    currentCategoryName: "",
  });

  const show = (stackOfIds, currentCategoryName) =>
    dispatch({
      type: SHOW_MODAL,
      stackOfIds,
      currentCategoryName,
    });
  const hide = () => dispatch({ type: HIDE_MODAL });
  const reset = () => dispatch({ type: RESET });

  return (
    <EditCategoryModalContext.Provider
      value={{
        isVisible: state.isVisible,
        stackOfIds: state.stackOfIds,
        currentCategoryName: state.currentCategoryName,
        show,
        hide,
        reset,
      }}
    >
      {children}
    </EditCategoryModalContext.Provider>
  );
};
