import IDataJson from "../../General/Interfaces/IDataJson";

export const getLocalIds = (
    localData: IDataJson[],
    listOfSavedIds: any[],
    listOfSeenIds: any[]
  ) => {
    Object.entries(localData).forEach(([k, v]) => {
      if (v && v.children) {
        getLocalIds(v.children, listOfSavedIds, listOfSeenIds);
      } else {
        if (v.saved) listOfSavedIds.push(v.id);
        if (v.seen) listOfSeenIds.push(v.id);
      }
    });
  };