import {
  Grid,
  Link,
  makeStyles,
  Typography,
  useTheme,
} from "@material-ui/core";
import firebase from "firebase/app";
import "firebase/auth";
import "firebase/firestore";
import { useEffect, useState } from "react";
import ReactPlayer from "react-player";
import { useHistory, useParams } from "react-router";
import { v5 as uuidv5 } from "uuid";
import { CardBottomRight } from "../Awesome/Cards/CardBottom/CardBottomRight";
import { useStoreVotingStatsToServer } from "../DataSource/Context/StoreVotingStatsToServer";
import { useGetRemoteVotingStats } from "../DataSource/getRemoteVotingStats";
import { useFetch } from "../DataSource/useFetch";
import { config } from "../firebase.config";
import { useFireBaseAuth } from "../General/Autorization/Context/FireBase";
import { useComponentSizes } from "../General/Context/ComponentSizesContext";
import { useShowSignInBar } from "../General/Context/ShowSignInBarContext";
import { useSignInModal } from "../General/Context/SignInModal";
import { useWindowWidth } from "../General/Context/WindowWidthContext";
import { TopAppBar } from "./Navigation/TopAppBar";

interface youtubeDataInterface {
  title: any;
  id: any;
  duration: any;
}

interface ratingInterface {
  [key: string]: {
    t: any;
    c: any;
  };
}
if (!firebase.apps.length) {
  firebase.initializeApp({ ...config });
}
const db = firebase.firestore();

export function YoutubeConverter() {
  let { youtubeId } = useParams<Record<string, string | undefined>>();
  const windowWidth = useWindowWidth();
  const theme = useTheme();
  const componentSizes = useComponentSizes();
  const showSignInBar = useShowSignInBar();
  const [currentVotingPosition, setCurrentVotingPosition] = useState(0);
  const [votingList, setVotingList] = useState<any>();
  const [remoteVotingStats, remoteVotingStatsLoading] =
    useGetRemoteVotingStats();
  const [showVotingMessage, setShowVotingMessage] = useState<any>(true);
  const [existsLocalSavedInArray, setExistsLocalSavedInArray] = useState<any[]>(
    []
  );
  const history = useHistory();
  const storeVotingStatsToServer = useStoreVotingStatsToServer();
  const remoteBookmarksStat = db.collection("voting").doc("stats");
  const increment = firebase.firestore.FieldValue.increment(1);
  const fireBaseAuth = useFireBaseAuth();
  const signInModal = useSignInModal();
  const [voted, setVoted] = useState(false);

  const useStyles = makeStyles((theme) => ({
    link: {
      textDecoration: "underline",
      color:
        windowWidth.width > 1024 && theme.palette.type === "light"
          ? "white"
          : theme.palette.text.primary,
    },
  }));
  const classes = useStyles();

  if (process.env.REACT_APP_DEBUG) console.log("theme", theme);
  const removeExistsLocalInArray = (cardId: any) => {
    setExistsLocalSavedInArray((prev) => {
      let clone = [...prev];
      let index = clone.findIndex((e) => e === cardId);
      if (index > -1) clone.splice(index, 1);

      return clone;
    });
  };

  const addExistsLocalInArray = (cardId: any) => {
    setExistsLocalSavedInArray((prev) => {
      let clone = [...prev, cardId];

      return clone;
    });
  };
  let leftBlockSize = windowWidth.width > 1024 ? 350 : 0;
  let playerMaxWidth = 800 + 16;
  let playerHeight = (800 - 16) * 0.5625;
  //if (windowWidth.width < 800) {
  playerHeight = (windowWidth.width - leftBlockSize) * 0.5625;
  //}
  const [data, loading] = useFetch(
    "https://kymano-86800.appspot.com/?id=" + youtubeId
  );
  const [youtubeData, setYoutubeData] = useState<youtubeDataInterface>();
  useEffect(() => {
    if (loading) {
      return;
    }

    if (process.env.REACT_APP_DEBUG) console.log("data", data);

    if (!data.title) {
      history.push("/");
      return;
    }
    data.id = uuidv5(data.title, "b5d1a120-ad90-52a9-8bfd-9cb55c023f55");
    data.youtubeId = youtubeId;
    data.fullVideo = true;
    data.url = "/y/" + youtubeId;
    data.start = 0;
    let match = data.duration.match(
      /PT(?:([0-9]+)H)?(?:([0-9]+)M)?(?:([0-9]+)S)?/
    );
    let endTime = 0;
    if (match[1]) {
      endTime += parseInt(match[1]) * 3600;
    }
    if (match[2]) {
      endTime += parseInt(match[2]) * 60;
    }
    if (match[3]) {
      endTime += parseInt(match[3]);
    }
    data.stop = endTime;
    setYoutubeData(data);
  }, [data, loading]);

  let navigationBbColor =
    theme.palette.type === "dark" ? theme.palette.background.paper : "#3a3a3a";

  useEffect(() => {
    if (remoteVotingStatsLoading || !remoteVotingStats) return;

    let list: ratingInterface = remoteVotingStats ?? {};
    let keysSorted = Object.entries(list!).sort(([, a], [, b]) => b.c - a.c);
    setCurrentVotingPosition(
      keysSorted.findIndex((e) => e[0] === youtubeId) + 1
    );

    setVotingList(keysSorted);
  }, [remoteVotingStats, remoteVotingStatsLoading]);

  const text = (
    <>
      We haven't created an awesome timeline like{" "}
      <Link
        href="#"
        onClick={() =>
          history.push("/alex-braine/javascript/full-course-for-beginners")
        }
        className={classes.link}
      >
        here
      </Link>{" "}
      for this video yet  It is number {currentVotingPosition} on the{" "}
      <Link
        href="#"
        onClick={() => setShowVotingMessage(false)}
        className={classes.link}
      >
        waiting list
      </Link>{" "}
      .{" "}
      {voted === false ? (
        <>
          Please{" "}
          <Link
            href="#"
            onClick={() => {
              if (fireBaseAuth.isSignIn === true) {
                db.runTransaction(async (t) => {
                  const collectionName: any = youtubeId;
                  t.update(remoteBookmarksStat, {
                    [collectionName + ".c"]: increment,
                    [collectionName + ".t"]: youtubeData!.title,
                  });
                });
                storeVotingStatsToServer.store();
                setVoted(true);
              } else {
                signInModal.show();
              }
            }}
            className={classes.link}
          >
            vote
          </Link>{" "}
          to raise it 
        </>
      ) : (
        <>You've voted. Thank you!</>
      )}
    </>
  );

  return (
    <>
      <TopAppBar
        youtubeData={youtubeData}
        bookmarkIcon={
          <>
            {youtubeData && (
              <CardBottomRight
                existsLocal={
                  existsLocalSavedInArray.find((e) => e === youtubeData.id)
                    ? true
                    : false
                }
                title={youtubeData.title}
                card={youtubeData}
                newStackOfTitles={["No category"]}
                stackOfIds={[0]}
                cardId={youtubeData.id}
                flexGrow={0}
                removeExistsLocalInArray={removeExistsLocalInArray}
                addExistsLocalInArray={addExistsLocalInArray}
                minWidth={735}
              />
            )}
          </>
        }
      />
      <div
        id="main"
        style={{
          display: "flex",
          flexDirection: windowWidth.width > 1024 ? "row" : "column",
          height:
            "calc(100% - " +
            (componentSizes.topAppBarHeight +
              (showSignInBar.status === true
                ? componentSizes.topSignInAppBarHeight
                : 0)) +
            "px)",
        }}
      >
        {currentVotingPosition !== 1 && (
          <>
            {windowWidth.width > 1024 ? (
              <div
                style={{
                  flexGrow: 0,
                  minWidth: 350,
                  backgroundColor: navigationBbColor,
                  display: "flex",
                }}
              >
                {showVotingMessage ? (
                  <span
                    style={{
                      maxWidth: 350,
                      padding: 16,
                      fontSize: "1rem",
                      color: "white",
                    }}
                  >
                    {currentVotingPosition !== 1 && <>{text}</>}
                  </span>
                ) : (
                  <div
                    style={{
                      maxWidth: 350,
                      padding: 16,
                      fontSize: "1rem",
                      color: "white",
                    }}
                  >
                    <ol>
                      {votingList.map((data: any, i: any) => {
                        return (
                          <li>
                            <Link
                              href="#"
                              onClick={() => history.push("/y/" + data[0])}
                            >
                              {data[1].t}
                            </Link>
                          </li>
                        );
                      })}
                    </ol>
                  </div>
                )}
              </div>
            ) : (
              <>
                <Typography variant="body1" style={{ padding: 8 }}>
                  {currentVotingPosition !== 1 && <>{text}</>}
                </Typography>
              </>
            )}
          </>
        )}
        <div
          style={{
            flexGrow: 1,
            display: "flex",
            flexDirection: "column",
            alignItems: "center",
          }}
        >
          <Grid
            container
            justify="space-between"
            style={{
              justifyContent: "space-between",
              alignItems: "center",
              flexWrap: "unset",
              height: playerHeight,
              maxWidth: "100%",
            }}
          >
            <ReactPlayer
              url={"https://www.youtube.com/watch?v=" + youtubeId}
              playing={true}
              controls={true}
              width="100%"
              height="100%"
            />
          </Grid>{" "}
        </div>
      </div>
    </>
  );
}
