import React, { useContext, useState } from "react";

const ProductHuntVotesContext = React.createContext();

export const useProductHuntVotes = () => {
  return useContext(ProductHuntVotesContext);
};

export const ProductHuntVotesProvider = ({ children }) => {
  const [votes, setVotes] = useState(0);

  const set = (votes) => setVotes(votes);

  return (
    <ProductHuntVotesContext.Provider value={{ votes, set }}>
      {children}
    </ProductHuntVotesContext.Provider>
  );
};