import React, { useContext, useReducer } from "react";

const MoveCardModalContext = React.createContext();

export const useMoveCardModal = () => {
  return useContext(MoveCardModalContext);
};

const SHOW_MODAL = "show";
const HIDE_MODAL = "hide";
const RESET = "reset";

const reducer = (state, action) => {
  switch (action.type) {
    case SHOW_MODAL: {
      return {
        isVisible: true,
        card: action.card,
        cardId: action.cardId,
        stackOfIds: action.stackOfIds,
        categoryTitle: action.categoryTitle,
        cats: action.cats,
      };
    }
    case HIDE_MODAL: {
      return { ...state, isVisible: false };
    }

    case RESET: {
      return {
        isVisible: false,
        card: {},
        cardId: 0,
        stackOfIds: [],
        categoryTitle: [],
        cats: [],
      };
    }

    default:
      return state;
  }
};

export const MoveCardModalProvider = ({ children }) => {
  const [state, dispatch] = useReducer(reducer, {
    isVisible: false,
    card: {},
    cardId: 0,
    stackOfIds: [],
    categoryTitle: [],
    cats: [],
  });

  const show = (card, cardId, stackOfIds, categoryTitle, cats) =>
    dispatch({
      type: SHOW_MODAL,
      card,
      cardId,
      stackOfIds,
      categoryTitle,
      cats,
    });
  const hide = () => dispatch({ type: HIDE_MODAL });
  const reset = () => dispatch({ type: RESET });

  return (
    <MoveCardModalContext.Provider
      value={{
        isVisible: state.isVisible,
        card: state.card,
        cardId: state.cardId,
        stackOfIds: state.stackOfIds,
        categoryTitle: state.categoryTitle,
        cats: state.cats,
        show,
        hide,
        reset
      }}
    >
      {children}
    </MoveCardModalContext.Provider>
  );
};
