import React, { useContext, useState } from "react";

const EditCategoryContext = React.createContext();

export const useEditCategory = () => {
  return useContext(EditCategoryContext);
};

export const EditCategoryProvider = ({ children }) => {
  const [state, setState] = useState({
    stackOfIds: [],
    newCategoryName: "",
  });

  const edit = (stackOfIds, newCategoryName) =>
    setState({ stackOfIds, newCategoryName });

  const reset = () => {
    setState({
      stackOfIds: [],
      newCategoryName: "",
    });
  };
  return (
    <EditCategoryContext.Provider
      value={{
        stackOfIds: state.stackOfIds,
        newCategoryName: state.newCategoryName,
        edit: edit,
        reset: reset,
      }}
    >
      {children}
    </EditCategoryContext.Provider>
  );
};
