import React, { useContext, useReducer } from "react";

const AddCategoryModalContext = React.createContext();

export const useAddCategoryModal = () => {
  return useContext(AddCategoryModalContext);
};

const SHOW_MODAL = "show";
const HIDE_MODAL = "hide";
const RESET = "reset";

const reducer = (state, action) => {
  switch (action.type) {
    case SHOW_MODAL: {
      return {
        isVisible: true,
        stackOfIds: action.stackOfIds,
        horizontal: true,
      };
    }
    case HIDE_MODAL: {
      return { ...state, isVisible: false };
    }
    case RESET: {
      return {
        isVisible: false,
        stackOfIds: [],
        horizontal: true,
      };
    }

    default:
      return state;
  }
};

export const AddCategoryModalProvider = ({ children }) => {
  const [state, dispatch] = useReducer(reducer, {
    isVisible: false,
    stackOfIds: [],
    horizontal: true,
  });

  const show = (stackOfIds, horizontal) =>
    dispatch({
      type: SHOW_MODAL,
      stackOfIds,
      horizontal,
    });
  const hide = () => dispatch({ type: HIDE_MODAL });
  const reset = () => dispatch({ type: RESET });

  return (
    <AddCategoryModalContext.Provider
      value={{
        isVisible: state.isVisible,
        stackOfIds: state.stackOfIds,
        horizontal: state.horizontal,
        show,
        hide,
        reset,
      }}
    >
      {children}
    </AddCategoryModalContext.Provider>
  );
};
