import React, { useState, useEffect, useRef } from "react";

import Button from "@material-ui/core/Button";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogTitle from "@material-ui/core/DialogTitle";
import TextField from "@material-ui/core/TextField";
import { useEditCategoryModal } from "../Context/EditCategoryModal";
import { useEditCategory } from "../Context/EditCategory";

export const EditCategoryModal = () => {
  const modal = useEditCategoryModal();
  const editCategory = useEditCategory();
  const [open, setOpen] = useState(false);
  useEffect(() => {
    setOpen(modal.isVisible);
  }, [modal]);

  const [inputValue, setInputValue] = useState("");

  useEffect(() => {
    setInputValue(modal.currentCategoryName);
  }, [modal]);

  const handleClose = () => {
    setOpen(false);
    modal.reset()
    //props.setOpenEditCategoryModal(false);
  };
  const handleCreate = () => {

    /* store.getItem("doc").then(function (value) {
      let val = value;
      
      let to = props.stackId;
      AddCard(to, val, card);
      
      });
    }); */


    editCategory.edit(modal.stackOfIds, titleRef.current.value)

    setOpen(false);
    modal.reset()
    //props.setOpenEditCategoryModal(false);
  };

  const titleRef = useRef();
  const onChangeHandler = (event) => {
    setInputValue(event.target.value);
  };

  return (
      <Dialog
        id={"edit-category-modal"}
        open={open}
        onClose={handleClose}
        disableBackdropClick={true}
        aria-labelledby="form-dialog-title"
      >
        <DialogTitle id="form-dialog-title">Edit category</DialogTitle>
        <DialogContent>
          <TextField
            required
            autoFocus
            id="name"
            label="Title"
            fullWidth
            inputRef={titleRef}
            onChange={onChangeHandler}
            value={inputValue}
          />
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose} color="primary">
            Cancel
          </Button>
          <Button
            id="save-category-button"
            onClick={handleCreate}
            color="primary"
          >
            Save
          </Button>
        </DialogActions>
      </Dialog>
  );
};
