import { deleteCardService } from "../../../MyCollection/Cards/Service/delCard";

export const handleDeleteCard = (
  setExistsLocal,
  id,
  store,
  stackOfIds,
  storeUserCollectionToServer
) => {
  setExistsLocal(false);

  if (process.env.REACT_APP_DEBUG) console.log('stackOfIds', stackOfIds);
  
  store.userCollection.getItem("doc").then(function (results) {
    let userCollection = [];
    if (results) {
      userCollection = results;
    }
    if (process.env.REACT_APP_DEBUG) console.log('handleDeleteCard1', userCollection, stackOfIds, id);
    deleteCardService(userCollection, stackOfIds, id);
    if (process.env.REACT_APP_DEBUG) console.log('handleDeleteCard2', userCollection, stackOfIds, id);


    store.userCollection.setItem("doc", userCollection).then(() => {
      storeUserCollectionToServer.store();
    });
  });
};
