const createFullPath = (path, card) => {
  if (path.length > 0) {
    return {
      title: path[0],
      children: [createFullPath(path.slice(1, path.length), card)],
    };
  } else {
    return card;
  }
};

export const addCard = (
  path,
  arr,
  card,
  stackOfLocalIds_,
  setStackOfLocalIds,
  stackOfLocalIdsFromStorage
) => {
  if (process.env.REACT_APP_DEBUG)
    console.log("addCard", path, arr, stackOfLocalIds_);

  if (undefined !== path[0]) {
    let found;
    Object.entries(arr).forEach(([k, v]) => {
      if (undefined !== v && null !== v && v.title === path[0]) {
        found = k;
      }
    });

    if (found !== undefined) {
      if (process.env.REACT_APP_DEBUG) console.log('addCard found', found);
      let newStackOfLocalIds = [];
      Array.prototype.push.apply(newStackOfLocalIds, stackOfLocalIds_);
      Array.prototype.push.apply(newStackOfLocalIds, [found]);

      const indx = arr[found].children.findIndex(
        (el) => undefined !== el.title && undefined === el.id
      );
      if (process.env.REACT_APP_DEBUG) console.log('addCard indx', indx);

      if (undefined !== arr[found].children && indx !== -1 && path.length !== 1) {
        if (process.env.REACT_APP_DEBUG) console.log('addCard arr[found].children', arr[found].children);

        addCard(
          path.slice(1, path.length),
          arr[found].children,
          card,
          newStackOfLocalIds,
          (ids) => setStackOfLocalIds(ids),
          stackOfLocalIdsFromStorage
        );
      } else if (path.length === 1) {
        let contains = Object.values(arr[found].children).some((elem) => {
          return card.id === elem.id;
        });

        if (!contains) {
          let nextId = 0;
          if (Object.keys(arr[found].children).length > 0) {
            const lastId = parseInt(
              Object.keys(arr[found].children)[
                Object.keys(arr[found].children).length - 1
              ]
            );
            nextId = lastId + 1;
          }

          arr[found].children[nextId] = card;

          if (process.env.REACT_APP_DEBUG)
            console.log("addCard stackOfLocalIds:::SET", newStackOfLocalIds);
          setStackOfLocalIds(newStackOfLocalIds);
        } else {
          Object.entries(arr[found].children).forEach(([k, value]) => {
            if (card.id === value.id) {
              arr[found].children[k] = card;
            }
          });
          if (process.env.REACT_APP_DEBUG)
            console.log("addCard stackOfLocalIds:::SET", newStackOfLocalIds);
          setStackOfLocalIds(newStackOfLocalIds);
        }
      } else {
        arr[found].children.push(createFullPath(path.slice(1), card));
        if (process.env.REACT_APP_DEBUG)
          console.log("addCard stackOfLocalIds:::SET", newStackOfLocalIds);
        setStackOfLocalIds(newStackOfLocalIds);
      }
    } else {
      let nextId = 0;
      if (Object.keys(arr).length > 0) {
        const lastId = parseInt(Object.keys(arr)[Object.keys(arr).length - 1]);
        nextId = lastId + 1;
      }

      arr[nextId] = createFullPath(path, card);

      let newStackOfLocalIds__ = [];
      Array.prototype.push.apply(newStackOfLocalIds__, stackOfLocalIds_);
      newStackOfLocalIds__.push(nextId.toString());

      for (let i = 0; i < path.length; i++) {
        newStackOfLocalIds__.push("0");
      }

      if (process.env.REACT_APP_DEBUG)
        console.log("addCard stackOfLocalIds:::SET", newStackOfLocalIds__);
      setStackOfLocalIds(newStackOfLocalIds__);
    }
  }
};
