import React, { useContext, useState } from "react";

const DeleteCardContext = React.createContext();

export const useDeleteCard = () => {
  return useContext(DeleteCardContext);
};

export const DeleteCardProvider = ({ children }) => {
  const [state, setState] = useState({ stackOfIds: [], cardId: undefined });

  const del = (stackOfIds, cardId) => {
    setState({ stackOfIds, cardId });
  };

  const reset = () => {
    setState({ stackOfIds: [], cardId: undefined });
  };

  return (
    <DeleteCardContext.Provider
      value={{
        stackOfIds: state.stackOfIds,
        cardId: state.cardId,
        delete: del,
        reset: reset,
      }}
    >
      {children}
    </DeleteCardContext.Provider>
  );
};
