import { useState, useEffect, useCallback } from "react";
import { useFireBaseAuth } from "../General/Autorization/Context/FireBase";
import firebase from "firebase/app";
import { useStoreBookmarksToServer } from "./Context/StoreBookmarksToServer";
import { useStoreBookmarksStateToServer } from "./Context/StoreBookmarksStatToServer";

export const useGetRemoteBookmarksStat = (): any => {
  const [data, setData] = useState<any>();
  const [loading, setLoading] = useState(true);
  const fireBaseAuth = useFireBaseAuth();
  const storeBookmarksStateToServer = useStoreBookmarksStateToServer() 

  const fetchData = useCallback(async () => {
    //if (fireBaseAuth.isSignIn) {
      const doc = await firebase
        .firestore()
        .collection("bookmarks")
        .doc("stat")
        .get();
        
      setData(doc.data());
      setLoading(false);
    //} else {
    //  setData(undefined);
    //  setLoading(false);
   // }
  }, [storeBookmarksStateToServer]);

  useEffect(() => {
    if (fireBaseAuth.loading === true) {
      return;
    }
    fetchData();
  }, [fetchData, fireBaseAuth]);
  return [data, loading];
};
