import { useEffect, useRef, useState } from "react";
import Toolbar from "@material-ui/core/Toolbar";
import { makeStyles, useTheme } from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";
import { MoveOnScrollTopAppBar } from "../../General/Navigation/MoveOnScrollTopAppBar";
import { useLocalForage } from "../../DataSource/localForageContext";
import { Right } from "../../General/Navigation/TopAppBar/Right";
import { BookmarkButton } from "../BookmarkButton";
import ArrowBackIcon from "@material-ui/icons/ArrowBack";
import IconButton from "@material-ui/core/IconButton";
import { useHistory } from "react-router-dom";
import { CardBottomRight } from "../Cards/CardBottom/CardBottomRight";
import { v5 as uuidv5 } from "uuid";

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
  title: {
    flexGrow: 1,
    display: "flex",
  },
}));

export function TopAppBar(props) {
  if (process.env.REACT_APP_DEBUG)
    console.log("props.stackOfLocalIds:", props.stackOfLocalIds);
  const classes = useStyles();
  const store = useLocalForage();
  const [title, setTitle] = useState("");
  const [id, setId] = useState("");
  const [back, setBack] = useState();
  const history = useHistory();
  const theme = useTheme();
  //const [existsLocalSavedInArray, setExistsLocalSavedInArray] = useState([]);
  const existsLocalSavedInArray2 = useRef([]);

  const removeExistsLocalInArray = (cardId) => {
    let clone = [...existsLocalSavedInArray2.current];
    let index = clone.findIndex((e) => e === cardId);
    if (index > -1) clone.splice(index, 1);
    existsLocalSavedInArray2.current = clone;
  };

  const addExistsLocalInArray = (cardId) => {
    let clone = [...existsLocalSavedInArray2.current, cardId];
    existsLocalSavedInArray2.current = clone;
  };

  useEffect(() => {
    if (process.env.REACT_APP_DEBUG)
      console.log("props.stackOfLocalIds: useEffect", props.stackOfLocalIds);

    if (
      undefined !== props.topAppBarData &&
      undefined !== props.topAppBarData.url
    ) {
      var urlSplit = props.topAppBarData.url.split("/");

      if (urlSplit.length > 3) {
        setBack(urlSplit.slice(0, -1).join("/"));
      } else {
        setBack("/browse");
      }
    }
    if (props.topAppBarData && props.topAppBarData.title) {
      props.topAppBarData.fullVideo = true;
      if (!props.topAppBarData.id) {
        setId(
          uuidv5(
            props.topAppBarData.title + Math.random(),
            "b5d1a120-ad90-52a9-8bfd-9cb55c023f55"
          )
        );
      } else {
        setId(props.topAppBarData.id);
      }
    }

    if (
      undefined !== props.topAppBarData &&
      undefined !== props.topAppBarData.title
    ) {
      setTitle(props.topAppBarData.title);
    }
  }, [props.topAppBarData]);

  if (props.existsLocalSavedInArray_) {
    existsLocalSavedInArray2.current = props.existsLocalSavedInArray_;
  }

  if (process.env.REACT_APP_DEBUG)
    console.log(
      "existsLocalSavedInArray2.current",
      existsLocalSavedInArray2.current,
      id
    );

  let color = theme.palette.type === "light" ? "black" : "white";

  return (
    <>
      {console.log(
        "existsLocalSavedInArray2.current",
        existsLocalSavedInArray2.current.find((e) => e === id) ? true : false
      )}
      <MoveOnScrollTopAppBar whiteForLight={true}>
        <Toolbar>
          {back && (
            <IconButton
              edge="start"
              onClick={() => {
                history.push(back);
              }}
            >
              <ArrowBackIcon />
            </IconButton>
          )}
          <Typography variant="h6" className={classes.title}>
            <span
              style={{
                lineHeight: "normal",
                display: "flex",
                alignItems: "center",
                justifyContent: "center",
              }}
            >
              {title}
            </span>
            {undefined !== props.topAppBarData &&
              undefined !== props.topAppBarData.title &&
              ((undefined !== props.topAppBarData.type &&
                props.topAppBarData.type !== "kit" &&
                props.topAppBarData.type !== "kitList") ||
                undefined === props.topAppBarData.type) && (
                <CardBottomRight
                  existsLocal={
                    existsLocalSavedInArray2.current.find((e) => e === id)
                      ? true
                      : false
                  }
                  title={title}
                  card={props.topAppBarData}
                  newStackOfTitles={
                    props.urlStackOfTitles
                      ? props.urlStackOfTitles[props.topAppBarData.url]
                      : ["No category"]
                  }
                  stackOfIds={
                    props.stackOfLocalIds ? props.stackOfLocalIds : [0]
                  }
                  cardId={id}
                  flexGrow={0}
                  removeExistsLocalInArray={removeExistsLocalInArray}
                  addExistsLocalInArray={addExistsLocalInArray}
                  minWidth={735}
                />
              )}
          </Typography>

          <Right />
        </Toolbar>
      </MoveOnScrollTopAppBar>
    </>
  );
}
