import Grid from "@material-ui/core/Grid";
import Box from "@material-ui/core/Box";
import MenuItem from "@material-ui/core/MenuItem";
import { TopAppBar } from "./Navigation/TopAppBar";
import { TopTabsMenu } from "../General/Navigation/TopTabsMenu";
//const worker = new Worker("/thread.worker.js");
import { useLocalForage } from "../DataSource/localForageContext";
import Card from "@material-ui/core/Card";
import CardHeader from "@material-ui/core/CardHeader";
import { makeStyles } from "@material-ui/core/styles";
import Avatar from "@material-ui/core/Avatar";
import { HorizontalCenterIconAndText } from "../General/Cards/HorizontalCenterIconAndText";

import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
