import { useTopTranformerTrigger } from "../General/Context/TopTranformerTriggerContext";
import { useWheelDirection } from "../General/Context/WheelDirectionContext";
import { useGetDocFromLocalForage } from "../DataSource/getLocalBookmarks";
import { TopTabsMenuSkeleton } from "../General/TopTabsMenuSkeleton";
import { CollectionLoader } from "../Loader/collection";
import { useGetRemoteBookmarks } from "../DataSource/getRemoteBookmarks";
import { useStoreBookmarksToServer } from "../DataSource/Context/StoreBookmarksToServer";
import { MenuButton } from "./Cards/CardBottom/MenuButton";
import { useGetRemoteBookmarksStat } from "../DataSource/getRemoteBookmarksStat";
import { CircularProgress } from "@material-ui/core";
import {CardsList} from "../General/Cards/CardsList";

export function Bookmarks() {
  const [setedUp, setup] = useState(false);
  const store = useLocalForage();
  const [bookmarks, setBookmarks] = useState({});
  const [setedUpBookmarks, setUpBookmarks] = useState(false);
  const history = useHistory();
  const wheelDirection = useWheelDirection();
  const [remoteData, remoteLoading] = useGetRemoteBookmarks("doc");
  const [localData, localLoading] = useGetDocFromLocalForage("doc");
  const [
    remoteBookmarksStat,
    remoteBookmarksStatLoading,
  ] = useGetRemoteBookmarksStat();
  const storeBookmarksToServer = useStoreBookmarksToServer();

  const topTranformerTrigger = useTopTranformerTrigger();
  const useStyles = makeStyles((theme) => ({
    action: {
      marginRight: 0,
      marginLeft: 16,
      marginTop: 4,
      color: theme.palette.text.secondary,
    },
    root: {
      padding: 16,
    },
    root2: {
      paddingTop: 0,
      paddingBottom: 0,
    },
    avatar: {
      alignSelf: "start",
    },
  }));

  const classes = useStyles();

  useEffect(() => {
    if (localLoading || remoteLoading) {
      return;
    }
    setup(true);
    if (localData === null || Object.keys(localData).length === 0) {
      if (remoteData) {
        
        setBookmarks(remoteData);
        setUpBookmarks(true);
        store.bookmarks.setItem("doc", remoteData);
      }
    } else {
      
      setBookmarks(localData);
      setUpBookmarks(true);
    }
  }, [store.bookmarks, localLoading, remoteLoading]);

  useEffect(() => {
    if (
      !remoteBookmarksStat ||
      bookmarks === null ||
      Object.keys(bookmarks).length === 0
    ) {
      return;
    }
    let clone = { ...bookmarks };
    Object.entries(clone).map(([k, v]) => {
      const key = k;
      const url = key.replace(/[~\*\/\[\]]/g, "#");
      v.b = remoteBookmarksStat[url] ?? 0;
    });
    
    setBookmarks(clone);
  }, [
    remoteBookmarksStat,
    localData,
    setedUpBookmarks,
    storeBookmarksToServer,
  ]);

  useLayoutEffect(() => {
    topTranformerTrigger.reset();
    wheelDirection.setDirection("down");
  }, []);

  const handleDeleteCard = (url) => {
    
    store.bookmarks.getItem("doc").then(function (bookmarks) {
      let clone = { ...bookmarks };
      
      //bookmarks = bookmarks.filter((key) => key !== url);
      delete clone[url];
      
      setBookmarks(clone);
      

      store.bookmarks.setItem("doc", clone).then(() => {
        storeBookmarksToServer.remove(url);
      });
    });
  };

  

  
  if (localLoading || remoteLoading || !setedUp) {
    return (
      <>
        <TopAppBar />
        <TopTabsMenuSkeleton />
        <CollectionLoader />
      </>
    );
  }

  return (
    <>
      <TopAppBar />
      <TopTabsMenu />
      <CardsList cards={bookmarks} handleDeleteCard={handleDeleteCard}/>
    </>
  );
}
