import React, { useContext, useReducer } from "react";

const EditCardModalContext = React.createContext();

export const useEditCardModal = () => {
  return useContext(EditCardModalContext);
};

const SHOW_MODAL = "show";
const HIDE_MODAL = "hide";
const RESET = "reset";

const reducer = (state, action) => {
  switch (action.type) {
    case SHOW_MODAL: {
      return {
        isVisible: true,
        card: action.card,
        cardId: action.cardId,
        stackOfIds: action.stackOfIds,
      };
    }
    case HIDE_MODAL: {
      return { ...state, isVisible: false };
    }

    case RESET: {
      return {
        isVisible: false,
        card: {},
        cardId: 0,
        stackOfIds: [],
      };
    }

    default:
      return state;
  }
};

export const EditCardModalProvider = ({ children }) => {
  const [state, dispatch] = useReducer(reducer, {
    isVisible: false,
    card: {},
    cardId: 0,
    stackOfIds: [],
  });

  const show = (card, stackOfIds) =>
    dispatch({
      type: SHOW_MODAL,
      card,
      stackOfIds,
    });
  const hide = () => dispatch({ type: HIDE_MODAL });
  const reset = () => dispatch({ type: RESET });

  return (
    <EditCardModalContext.Provider
      value={{
        isVisible: state.isVisible,
        card: state.card,
        cardId: state.cardId,
        stackOfIds: state.stackOfIds,
        show,
        hide,
        reset
      }}
    >
      {children}
    </EditCardModalContext.Provider>
  );
};
