import IconButton from "@material-ui/core/IconButton";
import { HorizontalCenterIconAndText } from "../../Cards/HorizontalCenterIconAndText";
import StarBorderIcon from "@material-ui/icons/StarBorder";
import MoreIcon from "@material-ui/icons/MoreVert";
import { useDrawer } from "../../Drawer/Context/Drawer";
import "./github-button.css";
import { useFetch } from "../../../DataSource/useFetch";
import { useEffect, useState } from "react";
import { usePrevious } from "../../usePrevious";
import { useGithubStars } from "../../Context/GithubStarsContext";
import { useWindowWidth } from "../../Context/WindowWidthContext";
import { makeStyles } from "@material-ui/core";
import { useProductHuntVotes } from "../../Context/ProductHuntVotesContext";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faDiscord } from "@fortawesome/free-brands-svg-icons";
import MenuIcon from "@material-ui/icons/Menu";
import { useDrawerWithMenu } from "../../DrawerWithMenu/Context/Drawer";
const minWidthForFullButtons = 460;
const minWidthForInlineButtons = 400;

const useStyles = makeStyles({
  inline: {
    display: "flex",
    alignItems: "center",
  },
  column: {
    display: "flex",
    flexDirection: "column",
    justifyContent: "space-around",
  },
});

export const Right = () => {
  const windowWidth = useWindowWidth();
  const drawer = useDrawerWithMenu();
  const githubStars = useGithubStars();
  const productHuntVotes = useProductHuntVotes();
  const [stars, setStars] = useState(githubStars.stars);
  const [votesPh, setVotesPh] = useState(productHuntVotes.votes);
  const [data, loading] = useFetch(
    "https://api.github.com/repos/kilteron/kilteron.github.io"
  );
  const [dataPh, loadingPh] = useFetch("https://kymano-86800.appspot.com/");

  useEffect(() => {
    if (loading) {
      return;
    }
    if (undefined !== data && data.stargazers_count) {
      setStars(data.stargazers_count);
      //etStars(111);
      githubStars.set(data.stargazers_count);
    } else if (undefined !== data && undefined === data.stargazers_count) {
      setStars(0);
    }
  }, [data, loading]);

  useEffect(() => {
    if (loadingPh) {
      return;
    }
    if (undefined !== dataPh && dataPh.votesCount) {
      //setStars(data.stargazers_count);
      setVotesPh(dataPh.votesCount);
      productHuntVotes.set(dataPh.votesCount);

      //githubStars.set(data.stargazers_count);
    } else if (undefined !== data && undefined === data.votesCount) {
      setVotesPh(0);
    }
  }, [dataPh, loadingPh]);

  const classes = useStyles();

  return (
    <div style={{ display: "flex" }}>
      <div
        className={
          windowWidth.width > minWidthForInlineButtons
            ? classes.inline
            : classes.column
        }
      >
        <div
          className="social-button"
          style={{ marginRight: 10, minWidth: 60 }}
        >
          <span className="github-btn github-stargazers">
            <a
              className="gh-btn"
              href="https://github.com/kilteron/kilteron.github.io"
              aria-label="Star kilteron/kilteron.github.io on GitHub"
            >
              <span
                className="gh-ico"
                aria-hidden="true"
                style={{
                  marginRight:
                    windowWidth.width > minWidthForFullButtons ? 4 : 0,
                }}
              ></span>{" "}
              {windowWidth.width > minWidthForFullButtons && (
                <span className="gh-text">Star</span>
              )}
            </a>
            {stars > 0 && (
              <a
                className="gh-count"
                href="https://github.com/kilteron/kilteron.github.io/stargazers"
                rel="noopener noreferrer"
                target="_blank"
                style={{ display: "block" }}
              >
                {stars}
              </a>
            )}
          </span>
        </div>
        <FontAwesomeIcon
          icon={faDiscord}
          style={{ fontSize: 20, cursor: "pointer" }}
          onClick={() => window.open("https://discord.gg/rQh6Ra2jgg", "_blank")}
        />
        {false && (
          <div className="social-button">
            <span className="github-btn github-stargazers">
              <a
                className="gh-btn"
                href="https://github.com/kilteron/kilteron.github.io"
                aria-label="Star kilteron/kilteron.github.io on GitHub"
              >
                <span
                  className="ph-ico"
                  aria-hidden="true"
                  style={{
                    marginRight:
                      windowWidth.width > minWidthForFullButtons ? 4 : 0,
                  }}
                ></span>
                {windowWidth.width > minWidthForFullButtons && (
                  <span className="gh-text">Vote</span>
                )}
              </a>
              {votesPh > 0 && (
                <a
                  className="gh-count"
                  href="https://github.com/kilteron/kilteron.github.io/stargazers"
                  rel="noopener noreferrer"
                  target="_blank"
                  style={{ display: "block" }}
                >
                  {votesPh}
                </a>
              )}
            </span>
          </div>
        )}
      </div>

      <div id="show-drawer">
        <IconButton
          aria-label="display more actions"
          edge="end"
          color="inherit"
          style={{ marginLeft: 0 }}
          onClick={() => drawer.show()}
        >
          <MenuIcon />
        </IconButton>
      </div>
    </div>
  );
};
