import { useRef, useEffect, useState } from "react";
import { makeStyles } from "@material-ui/core/styles";
import TextField from "@material-ui/core/TextField";
import Grid from "@material-ui/core/Grid";
import Button from "@material-ui/core/Button";
import firebase from 'firebase/app';
import 'firebase/auth'; 
import Paper from "@material-ui/core/Paper";
import { TopAppBar } from "./Navigation/TopAppBar";
import { TopTabsMenu } from "../General/Navigation/TopTabsMenu";
import Box from "@material-ui/core/Box";
import MenuItem from "@material-ui/core/MenuItem";
import { useLocalForage } from "../DataSource/localForageContext";
import VisibilityIcon from "@material-ui/icons/Visibility";
import BookmarkBorderIcon from "@material-ui/icons/BookmarkBorder";
import { useHistory } from "react-router-dom";
import Menu from "@material-ui/core/Menu";
import MoreVertIcon from "@material-ui/icons/MoreVert";
import Card from "@material-ui/core/Card";
import CardHeader from "@material-ui/core/CardHeader";
import Avatar from "@material-ui/core/Avatar";
import { HorizontalCenterIconAndText } from "../General/Cards/HorizontalCenterIconAndText";
import CardContent from "@material-ui/core/CardContent";
import CardActions from "@material-ui/core/CardActions";
import moment from "moment";
import Container from "@material-ui/core/Container";
import StorageIcon from "@material-ui/icons/Storage";

export default function Shared() {
  const [anchorEl, setAnchorEl] = useState(null);
  const store = useLocalForage();
  const [, setBookmarks] = useState({});

  const [listOfShared, setList] = useState([]);
  const titleRef = useRef();
  const urlRef = useRef();
  const descriptionRef = useRef();

  const doc = firebase
    .firestore()
    .collection("users")
    .doc("MdkMWbddc5hyTD6s29AByBzgDTx2");
  const history = useHistory();

  const useStyles = makeStyles((theme) => ({
    action: {
      marginRight: 0,
      color: theme.palette.text.secondary,
    },
    root: {
      padding: 16,
    },
  }));

  const useStyles2 = makeStyles((theme) => ({
    root: {
      paddingTop: 0,
      paddingBottom: 0,
    },
  }));
  const classes = useStyles();
  const classes2 = useStyles2();

  const handleMenu = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };
  const handleClick = () => {
    doc.get().then((data) => {
      const newSharedId = data.data().last_shared_id + 1;
      
      doc.collection("shared").doc(newSharedId.toString()).set({
        title: titleRef.current.value,
        url: urlRef.current.value,
        description: descriptionRef.current.value,
      });
      doc.update({
        last_shared_id: newSharedId,
      });
    });
  };
  

  const handleDeleteCard = (url) => {
    setAnchorEl(null);

    store.bookmarks.getItem("doc").then(function (bookmarks) {
      //bookmarks = bookmarks.filter((key) => key !== url);
      delete bookmarks[url];
      setBookmarks(bookmarks);
      store.bookmarks.setItem("doc", bookmarks).then(() => {});
    });
  };
  useEffect(() => {
    doc
      .collection("shared")
      .get()
      .then((docs) => {
        let list = [];
        docs.forEach((singleDoc) => {
          list.push({
            id: singleDoc.id,
            url: singleDoc.data().url,
            title: singleDoc.data().title,
            logo: singleDoc.data().logo,
            source: singleDoc.data().source,
            author: singleDoc.data().author,
            bookmarks: singleDoc.data().bookmarks,
            cards: singleDoc.data().cards,
            description: singleDoc.data().description,
            upd: singleDoc.data().upd,
          });
        });
        setList(list);
      });
  }, [doc]);

  return (
    <>
      <TopAppBar />
      <TopTabsMenu />
      <Container
        maxWidth="sm"
        style={{
          paddingLeft: 8,
          paddingRight: 8,
          display: "flex",
          flexDirection: "column",
        }}
      >
        <Grid
          container
          direction="column"
          justify="flex-start"
          alignItems="stretch"
          spacing={1}
        >
          {listOfShared && (
            <>
              {listOfShared.map((v) => (
                <>
                  <Grid item>
                    <Card className="card" id={"card"} variant="outlined">
                      <Box lineHeight="normal">
                        <CardHeader
                          classes={{ action: classes.action }}
                          titleTypographyProps={{ variant: "h6" }}
                          avatar={
                            undefined !== v.logo && (
                              <Avatar
                                style={{ cursor: "pointer" }}
                                src={v.logo}
                                onClick={() => history.push(v.source)}
                              ></Avatar>
                            )
                          }
                          action={
                            undefined !== v.author && (
                              <HorizontalCenterIconAndText>
                                <>
                                  <span
                                    onClick={() =>
                                      window.open(v.source, "_blank")
                                    }
                                  >
                                    {v.author}
                                  </span>
                                </>
                              </HorizontalCenterIconAndText>
                            )
                          }
                          title={
                            <>
                              <HorizontalCenterIconAndText>
                                <span onClick={() => history.push(v.url)}>
                                  {v.title}
                                </span>
                              </HorizontalCenterIconAndText>
                            </>
                          }
                          subheader={
                            undefined !== v.upd && (
                              <span>
                                {moment(
                                  v.upd,
                                  "YYYY-MM-DDTHH:mm:ssZ"
                                ).fromNow()}
                              </span>
                            )
                          }
                        />
                      </Box>
                      <CardContent classes={{ root: classes2.root }}>
                        <>{v.description}</>
                      </CardContent>
                      <CardActions classes={{ root: classes.root }}>
                        <>
                          <Grid
                            container
                            justify="space-between"
                            style={{
                              justifyContent: "space-between",
                              alignItems: "flex-end",
                            }}
                          >
                            <>
                              <Grid item>
                                <Grid
                                  container
                                  classes={{ container: classes.container }}
                                >
                                  {undefined !== v.bookmarks && (
                                    <HorizontalCenterIconAndText>
                                      <BookmarkBorderIcon
                                        style={{
                                          fontSize: 24,
                                          paddingRight: 5,
                                        }}
                                      />{" "}
                                      {v.bookmarks}
                                    </HorizontalCenterIconAndText>
                                  )}
                                  {undefined !== v.cards && (
                                    <HorizontalCenterIconAndText>
                                      <StorageIcon
                                        style={{
                                          fontSize: 25,
                                          paddingRight: 5,
                                          marginLeft: 10,
                                        }}
                                      />
                                      {v.cards}
                                    </HorizontalCenterIconAndText>
                                  )}
                                  {undefined !== v.url && (
                                    <HorizontalCenterIconAndText>
                                      <span
                                        style={{
                                          marginLeft: 20,
                                        }}
                                      >
                                        http://knogl.github.io/alex/{v.url}
                                      </span>
                                    </HorizontalCenterIconAndText>
                                  )}
                                </Grid>
                              </Grid>
                              <Grid item>
                                <Grid container item xs={12} justify="flex-end">
                                  <VisibilityIcon style={{marginRight:10}}/>
                                  <MoreVertIcon
                                    aria-owns={
                                      Boolean(anchorEl) ? "menu-appbar" : null
                                    }
                                    aria-haspopup="true"
                                    onClick={handleMenu}
                                    color="contrast"
                                  />
                                  <Menu
                                    id="menu-appbar"
                                    anchorEl={anchorEl}
                                    anchorOrigin={{
                                      vertical: "top",
                                      horizontal: "right",
                                    }}
                                    transformOrigin={{
                                      vertical: "top",
                                      horizontal: "right",
                                    }}
                                    open={Boolean(anchorEl)}
                                    onClose={handleClose}
                                  >
                                    <MenuItem
                                      onClick={() => handleDeleteCard(v.u)}
                                    >
                                      Hide
                                    </MenuItem>

                                    <MenuItem
                                      onClick={() => handleDeleteCard(v.u)}
                                    >
                                      Edit
                                    </MenuItem>
                                    <MenuItem
                                      onClick={() => handleDeleteCard(v.u)}
                                    >
                                      Delete
                                    </MenuItem>
                                  </Menu>
                                </Grid>
                              </Grid>
                            </>
                          </Grid>
                        </>
                      </CardActions>
                    </Card>
                  </Grid>
                </>
              ))}
            </>
          )}
          <Paper className={classes.paper}>
            <form className={classes.root} noValidate autoComplete="off">
              <div className={classes.root}>
                <Grid container spacing={3}>
                  <Grid item xs={12}>
                    <TextField
                      inputRef={titleRef}
                      id="standard-basic"
                      label="Title"
                    />
                  </Grid>
                  <Grid item xs={12}>
                    <TextField
                      inputRef={urlRef}
                      id="standard-basic"
                      label="Short title"
                    />
                  </Grid>
                  <Grid item xs={12}>
                    <TextField
                      inputRef={descriptionRef}
                      id="standard-basic"
                      label="Description"
                      multiline
                      rows={4}
                      variant="outlined"
                    />
                  </Grid>
                  <Grid item xs={12}>
                    <Button
                      variant="contained"
                      color="primary"
                      onClick={handleClick}
                    >
                      Primary
                    </Button>
                  </Grid>
                </Grid>
              </div>
            </form>
          </Paper>
        </Grid>
      </Container>
    </>
  );
}
