import { useState, useEffect } from "react";
import List from "@material-ui/core/List";
import Divider from "@material-ui/core/Divider";
import ListItem from "@material-ui/core/ListItem";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import ListItemText from "@material-ui/core/ListItemText";
import { useDrawerWithMenu } from "../Context/Drawer";
import Brightness4Icon from "@material-ui/icons/Brightness4";
import ExitToAppIcon from "@material-ui/icons/ExitToApp";
import firebase from "firebase/app";
import "firebase/auth";
import HomeIcon from "@material-ui/icons/Home";
import AssignmentIndIcon from "@material-ui/icons/AssignmentInd";
import { useFireBaseAuth } from "../../Autorization/Context/FireBase";
import BookmarkBorderIcon from "@material-ui/icons/BookmarkBorder";
import StorageIcon from "@material-ui/icons/Storage";
import FreeBreakfastIcon from "@material-ui/icons/FreeBreakfast";
import { useHistory } from "react-router-dom";
import Badge from "@material-ui/core/Badge";
import { HorizontalCenterIconAndText } from "../../Cards/HorizontalCenterIconAndText";
import StarBorderIcon from "@material-ui/icons/StarBorder";
import { Avatar, Drawer } from "@material-ui/core";
import { Backdrop, CircularProgress, makeStyles } from "@material-ui/core";
import BackupIcon from "@material-ui/icons/Backup";
import { General } from "../../DrawersGeneral/General";
import { useLocalForage } from "../../../DataSource/localForageContext";
import { useHashInInitialUrl } from "../../Context/HashInInitialUrlContext";
import { useHashChange } from "../../Context/HashChangeContext";
import BookmarkIcon from "@material-ui/icons/Bookmark";
import SettingsIcon from '@material-ui/icons/Settings';
const useStyles = makeStyles((theme) => ({
  backdrop: {
    zIndex: theme.zIndex.drawer + 1,
  },
}));
export function DrawerWithMenu(props) {
  const modal = useDrawerWithMenu();
  const store = useLocalForage();
  const [open, setOpen] = useState(false);
  const fireBaseAuth = useFireBaseAuth();
  const history = useHistory();
  const [openBackdrop, setOpenBackdrop] = useState(false);
  const [cardsNumberCurrent, setCardsNumberCurrent] = useState(0);
  const classes = useStyles();
  const hashInInitialUrl = useHashInInitialUrl();
  const hashChange = useHashChange();
  useEffect(() => {
    setOpen(modal.isVisible);
  }, [modal]);

  const handleClose = () => {
    modal.reset();
  };

  const resetHashes = () => {
    hashChange.setHash("");
    hashInInitialUrl.reset();
  };

  useEffect(() => {
    store.settings.getItem("cardsNumberCurrent").then(function (cardsNumber) {
      setCardsNumberCurrent(cardsNumber);
    });
  });

  return (
    <>
      <Drawer anchor="right" open={open} onClose={handleClose}>
        <div style={{ width: 250 }} role="presentation">
          {fireBaseAuth.isSignIn === true && (
            <div style={{ margin: 16 }}>
              <Avatar
                src={fireBaseAuth.userData.photoURL}
                style={{ marginBottom: 8, width: 45, height: 45 }}
              />
              {fireBaseAuth.userData.displayName ?? fireBaseAuth.userData.email}
            </div>
          )}
          <List>
            <ListItem
              button
              onClick={() => {
                setOpen(false);
                resetHashes();

                setTimeout(() => {
                  history.push("/");
                  modal.reset();
                }, 50);
              }}
            >
              <ListItemIcon>
                <div id="drawer-with-menu-badge">
                  <HomeIcon />
                </div>
              </ListItemIcon>
              <div id="drawer-with-menu-my-collection">
                <ListItemText primary={"Home"} />
              </div>
            </ListItem>
          </List>
          <List>
            <ListItem
              button
              onClick={() => {
                setOpen(false);
                resetHashes();

                setTimeout(() => {
                  history.push("/saved");
                  modal.reset();
                }, 50);
              }}
            >
              <ListItemIcon>
                <div id="drawer-with-menu-badge">
                  <Badge badgeContent={cardsNumberCurrent} color="secondary">
                    <BookmarkIcon />
                  </Badge>
                </div>
              </ListItemIcon>
              <div id="drawer-with-menu-my-collection">
                <ListItemText primary={"Saved"} />
              </div>
            </ListItem>
          </List>

          <List>
            <ListItem
              button
              onClick={() => {
                setOpen(false);
                resetHashes();

                setTimeout(() => {
                  history.push("/browse");
                  modal.reset();
                }, 50);
              }}
            >
              <ListItemIcon>
                <StorageIcon />
              </ListItemIcon>
              <div id="drawer-with-menu-browse">
                <ListItemText primary={"Browse"} />
              </div>
            </ListItem>
          </List>

          <List>
            <ListItem
              button
              onClick={() => {
                setOpen(false);
                resetHashes();

                setTimeout(() => {
                  history.push("/settings");
                  modal.reset();
                }, 50);
              }}
            >
              <ListItemIcon>
                <SettingsIcon />
              </ListItemIcon>
              <div id="drawer-with-menu-settings">
                <ListItemText primary={"Settings"} />
              </div>
            </ListItem>
          </List>

          {fireBaseAuth.isSignIn === true ? (
            <List>
              <ListItem
                button
                onClick={() => {
                  setOpen(false);
                  resetHashes();
                  setTimeout(() => {
                    history.push("/backups");
                    modal.reset();
                  }, 50);
                }}
              >
                <ListItemIcon>
                  <BackupIcon />
                </ListItemIcon>
                <ListItemText primary={"Backups"} />
              </ListItem>
            </List>
          ) : (
            ""
          )}

          <Divider />
          <General
            setOpenBackdrop={setOpenBackdrop}
            setOpen={setOpen}
            toggleDarkTheme={props.toggleDarkTheme}
            theme={props.theme}
            modal={modal}
            showBackups={false}
          />
        </div>
      </Drawer>
      <Backdrop
        open={openBackdrop}
        className={classes.backdrop}
        style={{
          backgroundColor:
            props.theme.palette.type === "light" ? "#000" : "#fff",
        }}
      >
        <CircularProgress color="inherit" />
      </Backdrop>
    </>
  );
}
