import  { useState, useEffect } from "react";
import { useAlert } from "./AlertContext";
import Snackbar from "@material-ui/core/Snackbar";
import MuiAlert from "@material-ui/lab/Alert";

export function Alert() {
  const alert = useAlert();
  const [open, setOpen] = useState(alert.isVisible);
  useEffect(() => {
    setOpen(alert.isVisible);
  }, [alert]);

  return (
    <div>
      <Snackbar
        id="snackRoot"
        onWheel={() => {
          setOpen(false);
          document.body.scrollTop += 20;

          //scrollTo(document.body, document.body.scrollTop+20, 1)
          //window.scrollTo({top: document.body.scrollTop+20, behavior: 'smooth'});
        }}
        onTouchMove={() => {
          setOpen(false);
          document.body.scrollTop += 20;
          //window.scrollTo({
          //  top: document.body.scrollTop + 20,
          //  behavior: "smooth",
          //});
        }}
        onTouchCancel={() => {}}
        onTouchEnd={() => {}}
        onTouchStart={() => {}}
        anchorOrigin={{
          vertical: "top",
          horizontal: "center",
        }}
        onClose={() => {
          setOpen(false);
        }}
        open={open}
        autoHideDuration={5000}
        message="Note archived"
      >
        <MuiAlert elevation={6} variant="filled" severity="success">
          {alert.text}
        </MuiAlert>
      </Snackbar>
    </div>
  );
}
