import { Container } from "@material-ui/core";
import { useEffect, useState } from "react";
import { TopAppBar } from "./Navigation/TopAppBar";
import firebase from "firebase/app";
import "firebase/firestore";
import "firebase/auth";
import { config } from "../firebase.config";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import ListItemSecondaryAction from "@material-ui/core/ListItemSecondaryAction";
import ListItemText from "@material-ui/core/ListItemText";
import IconButton from "@material-ui/core/IconButton";
import { Paper } from "@material-ui/core";
import Switch from "@material-ui/core/Switch";
import { useLocalForage } from "../DataSource/localForageContext";

if (!firebase.apps.length) {
  firebase.initializeApp({ ...config });
}

const db = firebase.firestore();

export function MySettings() {
  const [showLoop, setShowLoop] = useState<any>(false);
  const store = useLocalForage();

  useEffect(() => {
    store.settings.getItem("loopVideo").then(function (status: boolean) {
      if (process.env.REACT_APP_DEBUG) console.log("status", status);
      setShowLoop(status);
    });
  }, []);

  const updShowLoop = (status: boolean) => {
    setShowLoop(status);
    store.settings.setItem("loopVideo", status).then(() => {

    });
  };

  if (process.env.REACT_APP_DEBUG) console.log('showLoop>>>>', showLoop);

  return (
    <>
      <TopAppBar />

      <Container
        maxWidth="sm"
        style={{
          paddingLeft: 8,
          paddingRight: 8,
          display: "flex", 
          flexDirection: "column",
          marginTop: 10,
        }}
      >
        <Paper variant="outlined">
          <List>
            <ListItem>
              <ListItemText primary="Loop" />
              <ListItemSecondaryAction>
                  <Switch 
                    checked={showLoop}
                    onChange={() => updShowLoop(!showLoop)}
                    name="checkedA"
                    inputProps={{ "aria-label": "secondary checkbox" }}
                  />
              </ListItemSecondaryAction>
            </ListItem>
          </List>
        </Paper>
      </Container>
    </>
  );
}
