import React, { useContext, useReducer } from "react";

const SkeletonLoaderForHashNavigationContext = React.createContext();

export const useSkeletonLoaderForHashNavigation = () => {
  return useContext(SkeletonLoaderForHashNavigationContext);
};

const TURN_ON = "TURN_ON";
const TURN_OFF = "TURN_OFF";

const reducer = (state, action) => {
  switch (action.type) {
    case TURN_ON:
      return { ...state, status: "ON" };
    case TURN_OFF:
      return { ...state, status: "OFF" };
    default:
      return state;
  }
};

export const SkeletonLoaderForHashNavigationProvider = ({ children }) => {
  const [state, dispatch] = useReducer(reducer, { status: "OFF" });

  const turnOn = () => dispatch({ type: TURN_ON });
  const turnOff = () => dispatch({ type: TURN_OFF });

  return (
    <SkeletonLoaderForHashNavigationContext.Provider
      value={{
        status: state.status,
        turnOn,
        turnOff,
      }}
    >
      {children}
    </SkeletonLoaderForHashNavigationContext.Provider>
  );
};
