import { useState, useEffect } from "react";
import { useLocalForage } from "./localForageContext";
import { IDataJson } from "../General/Interfaces/IDataJson";

export const useGetDocFromLocalForage = (key) => {
  const [data, setData] = useState(IDataJson);
  const [loading, setLoading] = useState(true);
  const store = useLocalForage();

  useEffect(() => {
    async function fetchData() {
      const response = await store.userCollection.getItem(key);
      setData(response);
      setLoading(false);
    }
    fetchData();
  }, [key, store]);

  return [data, loading];
};
