import ICollectionNavigation from "../Interface/ICollectionNavigation";

export const navigationToUrlStackOfTitles = (
  arr: ICollectionNavigation[],
  stackOfTitles: any[],
  urlStackOfTitles: any
) => {
  Object.entries(arr).forEach(([k, v]) => {
    if (v.title) {
      let newstackOfTitles: any[] = [];
      Array.prototype.push.apply(newstackOfTitles, stackOfTitles);
      Array.prototype.push.apply(newstackOfTitles, [v.title]);
      urlStackOfTitles[v.url] = newstackOfTitles;
      if (v.children) {
        navigationToUrlStackOfTitles(
          v.children,
          newstackOfTitles,
          urlStackOfTitles
        );
      }
    }
  });
};
