import React, { useContext, useState } from "react";

const VideoProgressBarContext = React.createContext();

export const useVideoProgressBar = () => {
  return useContext(VideoProgressBarContext);
};

export const VideoProgressBarProvider = ({ children }) => {
  const [progress, setProgress] = useState(0);

  const set = (progress) => setProgress(progress);

  return (
    <VideoProgressBarContext.Provider value={{ progress, set }}>
      {children}
    </VideoProgressBarContext.Provider>
  );
};
