import React, { useState, useEffect, useRef } from "react";
import Button from "@material-ui/core/Button";
import Grid from "@material-ui/core/Grid";
import { useLocalForage } from "../../../DataSource/localForageContext";
import { useShowSignInBar } from "../../../General/Context/ShowSignInBarContext";
import { handleAddCard } from "../Service/handleAddCard";
import { handleDeleteCard } from "../Service/handleDeleteCard";
import { useStoreUserCollectionToServer } from "../../../DataSource/Context/StoreUserCollectionToServer";
import { useWindowWidth } from "../../../General/Context/WindowWidthContext";
import { IconButton, makeStyles, useTheme } from "@material-ui/core";
import { useCardsNumberInUserCollection } from "../../../General/Context/CardsNumberInUserCollectionContext";
import { useFireBaseAuth } from "../../../General/Autorization/Context/FireBase";
import BookmarkIcon from "@material-ui/icons/Bookmark";
import BookmarkBorderIcon from "@material-ui/icons/BookmarkBorder";
const useStyles = makeStyles((theme) => ({
  root: {
    color: theme.palette.text.secondary,
  },
}));

export function CardBottomRight(props) {
  if (process.env.REACT_APP_DEBUG) console.log('CardBottomRight', props);
  const store = useLocalForage();
  const showSignInBar = useShowSignInBar();
  const storeUserCollectionToServer = useStoreUserCollectionToServer();
  const fireBaseAuth = useFireBaseAuth();
  const classes = useStyles();
  const theme = useTheme();
  const [existsLocal, setExistsLocal] = useState(false);
  const [stackOfLocalIds, setStackOfLocalIds] = useState([]);
  const stackOfLocalIdsFromStorage = useRef([]);
  const windowWidth = useWindowWidth();
  const cardsNumberInUserCollection = useCardsNumberInUserCollection();

  if (process.env.REACT_APP_DEBUG) console.log("props::::", props);

  const bColor = {
    dark: theme.palette.primary.contrastText,
    light: theme.palette.common.white,
  };

  const setStackOfLocalIds_ = (stackOfLocalIds) => {
    if (process.env.REACT_APP_DEBUG)
      console.log("stackOfLocalIds:", stackOfLocalIds);
    setStackOfLocalIds(stackOfLocalIds);
  };

  useEffect(() => {
    if (undefined !== props.existsLocal) {
      setExistsLocal(props.existsLocal);
    }
    if (undefined !== props.stackOfLocalIds) {
      if (process.env.REACT_APP_DEBUG)
        console.log("stackOfLocalIds:", props.stackOfLocalIds);

      stackOfLocalIdsFromStorage.current = props.stackOfLocalIds;
      setStackOfLocalIds(props.stackOfLocalIds);
    } else {
      if (process.env.REACT_APP_DEBUG)
        console.log("stackOfLocalIds:", props.stackOfIds);

      stackOfLocalIdsFromStorage.current = props.stackOfIds;
      setStackOfLocalIds(props.stackOfIds);
    }
  }, [props]);

  useEffect(() => {
    if (process.env.REACT_APP_DEBUG)
      console.log("stackOfLocalIds:::>>", stackOfLocalIds);
  }, [stackOfLocalIds]);

  const saveText =
    windowWidth.width <= props.minWidth ? "Save" : "Save for later";
  if (process.env.REACT_APP_DEBUG)
    console.log(
      "stackOfLocalIds:::?" + existsLocal,
      props.title,
      stackOfLocalIds,
      props.stackOfIds,
      props.stackOfLocalIds
    );

  return (
    <Grid item style={{ flexGrow: props.flexGrow ?? 2, marginRight: 4 }}>
      <Grid container item xs={12} justify="flex-end">
        {existsLocal ? (
          <IconButton
            id={"delete-card"}
            className={classes.root}
            variant="outlined"
            style={{ position: "static", marginLeft: "auto" }}
            onClick={(e) => {
              e.stopPropagation();
              handleDeleteCard(
                setExistsLocal,
                props.cardId,
                store,
                stackOfLocalIds,
                storeUserCollectionToServer
              );
              if (props.removeExistsLocalInArray) {
                props.removeExistsLocalInArray(props.cardId);
              }
            }}
          >
            {/* Delete */}
            <BookmarkIcon
              style={{
                color:
                  theme.palette.type === "light"
                    ? theme.palette.common.white
                    : theme.palette.primary.contrastText,
              }}
            />
          </IconButton>
        ) : (
          <>
            {
              <IconButton
                id={"save-card"}
                variant="outlined"
                style={{ position: "static", marginLeft: "auto" }}
                onClick={(e) => {
                  e.stopPropagation();
                  props.card.saved = true;
                  if (props.cardsCollection) {
                    props.card.fullVideo = true;
                  }
                  handleAddCard(
                    stackOfLocalIdsFromStorage,
                    setExistsLocal,
                    store,
                    props.card,
                    props.title,
                    props.newStackOfTitles,
                    setStackOfLocalIds,
                    showSignInBar,
                    storeUserCollectionToServer,
                    cardsNumberInUserCollection,
                    fireBaseAuth
                  );
                  if (props.addExistsLocalInArray) {
                    props.addExistsLocalInArray(props.cardId);
                  }
                }}
              >
                {/* {saveText} */}
                <BookmarkBorderIcon
                  style={{
                    color:
                      theme.palette.type === "light"
                        ? theme.palette.common.white
                        : theme.palette.primary.contrastText,
                  }}
                />
              </IconButton>
            }
          </>
        )}
      </Grid>
    </Grid>
  );
}
