import { useState, useEffect, useCallback } from "react";
import { useFireBaseAuth } from "../General/Autorization/Context/FireBase";
import firebase from 'firebase/app';

export const useGetRemoteBackups = () => {
  const [data, setData] = useState();
  const [loading, setLoading] = useState(true);
  const fireBaseAuth = useFireBaseAuth();

  const fetchData = useCallback(async () => {
    if (fireBaseAuth.isSignIn) {
      const doc = await firebase
        .firestore()
        .collection("users")
        .doc(fireBaseAuth.uid)
        .collection("backups")
        .doc("list")
        .get();

      setData((doc.data()));
      
      setLoading(false);
    } else {
      setData(undefined);
      setLoading(false);
    }
  }, [fireBaseAuth]);

  useEffect(() => {
    if (fireBaseAuth.loading === true) {
      return;
    }
    fetchData();
  }, [fetchData, fireBaseAuth]);
  return [data, loading];
};
