import React, { useState, useEffect, useRef } from "react";
import Button from "@material-ui/core/Button";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogTitle from "@material-ui/core/DialogTitle";
import TextField from "@material-ui/core/TextField";
import { useAddCardModal } from "../Context/AddCardModal";
import { useAddCard } from "../Context/AddCard";
import { v5 as uuidv5 } from "uuid";

export function AddCardModal() {
  const modal = useAddCardModal();
  const addCard = useAddCard();
  const [open, setOpen] = useState(false);
  useEffect(() => {
    setOpen(modal.isVisible);
  }, [modal]);

  const handleClose = () => {
    setOpen(false);
    modal.reset();
  };
  const handleCreate = () => {
    const card = {
      id: uuidv5(
        titleRef.current.value + Math.random(),
        "51949bf6-e543-411c-a608-30fc1055feec"
      ),
      title: titleRef.current.value,
      description: descriptionRef.current.value,
      url: urlRef.current.value,
    };
    setOpen(false);
    addCard.add(card, modal.stackOfIds);
    modal.reset();
  };

  const titleRef = useRef();
  const descriptionRef = useRef();
  const urlRef = useRef();
  const fullDescriptionRef = useRef();

  return (
    <Dialog
      id={"add-card-modal"}
      open={open}
      onClose={handleClose}
      disableBackdropClick={true}
      aria-labelledby="form-dialog-title"
    >
      <DialogTitle id="form-dialog-title">Add new card</DialogTitle>
      <DialogContent>
        <TextField
          required
          autoFocus
          id="name"
          label="Title"
          fullWidth
          inputRef={titleRef}
        />

        <TextField
          required
          autoFocus
          id="name"
          multiline
          label="Description"
          rows={4}
          fullWidth
          inputRef={descriptionRef}
        />
        <TextField
          autoFocus
          margin="dense"
          id="name"
          label="Url"
          fullWidth
          inputRef={urlRef}
        />
        <TextField
          autoFocus
          margin="dense"
          id="name"
          label="Full description"
          rows={7}
          multiline
          fullWidth
          inputRef={fullDescriptionRef}
        />
      </DialogContent>
      <DialogActions>
        <Button onClick={handleClose} color="primary">
          Cancel
        </Button>
        <Button
          onClick={handleCreate}
          id={"create-card-button"}
          color="primary"
        >
          Create
        </Button>
      </DialogActions>
    </Dialog>
  );
}
