import { addCard } from "./addCard";
import { CARDS_NUMBER_WHEN_SHOW_SIGN_IN_BAR } from "../../../consts";

export const handleAddCard = (
  stackOfLocalIdsFromStorage,
  setExistsLocal,
  store,
  cardData,
  title,
  newStackOfTitles,
  setStackOfLocalIds,
  showSignInBar,
  storeUserCollectionToServer,
  cardsNumberInUserCollection,
  fireBaseAuth
) => {
  if (process.env.REACT_APP_DEBUG)
    console.log("handleAddCard", stackOfLocalIdsFromStorage, cardData);
  //enqueueSnackbar("Saved. You have 13 cards in your collection", {
  //  variant: "success",
  //});

  setExistsLocal(true);
  //setAnchorEl(null);

  store.userCollection.getItem("doc").then(function (results) {
    let userCollection = [];
    if (results) {
      userCollection = results;
    }
    const card = {
      id: cardData.id,
      saved: cardData.saved,
      title: cardData.title,
      url: cardData.url,
      avatar: cardData.avatar,
      license: cardData.license,
      description: cardData.description,
      hideDescription: cardData.hideDescription,
      hideTitleAndDescription: cardData.hideTitleAndDescription,
      stars: cardData.stars,
      updatedAt: cardData.updatedAt,
      createdAt: cardData.createdAt,
      youtubeId: cardData.youtubeId,
      start: cardData.start,
      stop: cardData.stop,
      gistId: cardData.gistId,
      gistFile: cardData.gistFile,
      documentation: cardData.documentation,
      laboratory: cardData.laboratory,
      seen: cardData.seen,
      fullVideo: cardData.fullVideo,
      source: cardData.source, 
    };
    let path = [...newStackOfTitles];
    //path.unshift(props.collectionName);

    if (process.env.REACT_APP_DEBUG) console.log('card:::', card);
    addCard(
      path,
      userCollection,
      card,
      [],
      setStackOfLocalIds,
      stackOfLocalIdsFromStorage
    );

    /*   const getCardsNumber = (a, number = 0) => {
        
        if (typeof a === "object" && a !== null) {
          for (const [, value] of Object.entries(a)) {
            if (undefined !== value) {
              if (undefined !== value.children) {
                number = getCardsNumber(value.c, number);
              }
              if (undefined !== value.title) {
                number++;
              }
            }
          }
          return number
        }
      }; */

    /* alert.show(
        "Saved. You have " +
          (cardsNumberInUserCollection.number + 1) +
          " cards."
      ); */
    store.settings.getItem("cardsNumberTotal").then(function (cardsNumber) {
      if (
        cardsNumber > CARDS_NUMBER_WHEN_SHOW_SIGN_IN_BAR - 1 &&
        showSignInBar.status === false &&
        fireBaseAuth.loading === false &&
        fireBaseAuth.isSignIn === false
      ) {
        showSignInBar.show();
      }
      store.settings.setItem("cardsNumberTotal", cardsNumber + 1);
    });

    store.settings.getItem("cardsNumberCurrent").then(function (cardsNumber) {
      store.settings.setItem("cardsNumberCurrent", cardsNumber + 1).then(() => {
        cardsNumberInUserCollection.update();
      });
    });

    store.userCollection
      .setItem("doc", userCollection)
      .then(() => {
        //doc.set({ data: JSON.stringify(val) }).then(() => {});
        storeUserCollectionToServer.store();
      })
      .catch(function (err) {});
  });
};
