import React, { useContext, useReducer } from "react";

const DeleteCategoryModalContext = React.createContext();

export const useDeleteCategoryModal = () => {
  return useContext(DeleteCategoryModalContext);
};

const SHOW_MODAL = "show";
const HIDE_MODAL = "hide";
const RESET = "reset";

const reducer = (state, action) => {
  switch (action.type) {
    case SHOW_MODAL: {
      return {
        isVisible: true,
        stackOfIds: action.stackOfIds,
        title: action.title,
      };
    }
    case HIDE_MODAL: {
      return { ...state, isVisible: false };
    }

    case RESET: {
      return {
        isVisible: false,
        stackOfIds: [],
        title: "",
      };
    }

    default:
      return state;
  }
};

export const DeleteCategoryModalProvider = ({ children }) => {
  const [state, dispatch] = useReducer(reducer, {
    isVisible: false,
    stackOfIds: [],
    title: "",
  });

  const show = (stackOfIds, title) =>
    dispatch({
      type: SHOW_MODAL,
      stackOfIds,
      title,
    });
  const hide = () => dispatch({ type: HIDE_MODAL });
  const reset = () => dispatch({ type: RESET });

  return (
    <DeleteCategoryModalContext.Provider
      value={{
        isVisible: state.isVisible,
        stackOfIds: state.stackOfIds,
        title: state.title,
        show,
        hide,
        reset,
      }}
    >
      {children}
    </DeleteCategoryModalContext.Provider>
  );
};
