import { useState } from "react";
import Menu from "@material-ui/core/Menu";
import { IconButton, MenuItem } from "@material-ui/core";
import MoreVertIcon from "@material-ui/icons/MoreVert";

export const MenuButton = ({ id, handleDeleteCard }) => {
  const [anchorEl, setAnchorEl] = useState(null);

  const handleMenu = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  const deleteCard = () => {
    setAnchorEl(null);
    handleDeleteCard(id);
  };

  return (
    <div>
      <IconButton onClick={handleMenu}>
        <MoreVertIcon
          id={"open-card-menu-" + id}
          aria-owns={Boolean(anchorEl) ? "menu-appbar" : null}
          aria-haspopup="true"
          color="contrast"
        />
      </IconButton>
      <Menu
        id="menu-appbar"
        anchorEl={anchorEl}
        anchorOrigin={{
          vertical: "top",
          horizontal: "right",
        }}
        transformOrigin={{
          vertical: "top",
          horizontal: "right",
        }}
        open={Boolean(anchorEl)}
        onClose={handleClose}
      >
        <MenuItem id={"delete-card-" + id} onClick={deleteCard}>
          Delete
        </MenuItem>
      </Menu>
    </div>
  );
};
