export function UpdateInterfaceBasedOnLocalData() {
  //const cardsNumberInUserCollection = useCardsNumberInUserCollection();
  const showSignInBar = useShowSignInBar();
  const fireBaseAuth = useFireBaseAuth();
  const store = useLocalForage();

  useEffect(() => {
    if (showSignInBar.status === true) return;
    /*     let cardsNumber = 0;
    if (localData) {
      cardsNumber = getCardsNumber(localData);
    } */

    //cardsNumberInUserCollection.set(cardsNumber);
    store.settings.getItem("cardsNumberTotal").then(function (cardsNumber) {
      
      if (
        cardsNumber > CARDS_NUMBER_WHEN_SHOW_SIGN_IN_BAR - 1 &&
        fireBaseAuth.loading === false &&
        fireBaseAuth.isSignIn === false
      ) {
        showSignInBar.show();
      }
    });
  }, [fireBaseAuth, showSignInBar]);

  return <></>;
}
