import React, { useContext, useState } from "react";

const SaveCardContext = React.createContext();

export const useSaveCard = () => {
  return useContext(SaveCardContext);
};

export const SaveCardProvider = ({ children }) => {
  const [state, setState] = useState({ card: {}, stackOfIds: [] });

  const save = (card, stackOfIds) => setState({ card, stackOfIds });

  const reset = () => {
    setState({ card: {}, stackOfIds: [] });
  };

  return (
    <SaveCardContext.Provider
      value={{ card: state.card, stackOfIds: state.stackOfIds, save: save, reset: reset }}
    >
      {children}
    </SaveCardContext.Provider>
  );
};
